import React, { useEffect, useState } from 'react';
import { connect } from 'react-redux';
import { Loader } from 'shared/components/loaders';
import { Center, Switch } from 'shared/components';
import { loadAllAccountsThunk } from 'explorer/store/thunks';
import SvgIcon from '@material-ui/core/SvgIcon';
import Icon from '@mdi/react';
import { mdiDns, mdiServer } from '@mdi/js';
import { CircleImg, NumberLabel } from '../../../shared/components';
import './ServersList.local.scss';
import { Link } from 'react-router-dom';
import AddIcon from '@material-ui/icons/Add';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import { AddServerCard } from '../../../explorer/containers/AddServerCard/AddServerCard';
import { AddDatabaseCard } from '../../../explorer/containers/AddDatabaseCard/AddDatabaseCard';
import { ImportCSV } from '../../../explorer/containers/ImportCSV/ImportCSV';
import Button from '@material-ui/core/Button';

export const ServersList = ({ loading, dbms, getAllAccounts }) => {
  const [anchorElement, setAnchorElement] = useState(null);

  useEffect(() => {
    getAllAccounts();
  }, []);

  const handleClick = (event) => {
    event.stopPropagation();
    setAnchorElement(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorElement(null);
  };

  const serversNumber = dbms.reduce((acc, el) => acc + el.servers.length, 0);

  return (
    <Switch value={loading}>
      <Switch.Case value>
        <Center>
          <Loader>Loading Servers</Loader>
        </Center>
      </Switch.Case>
      <Switch.Case value={false}>
        <header onClick={handleClose} role="presentation">
          <div className="widget-title">
            <CircleImg
              image={
                <SvgIcon>
                  <Icon path={mdiServer} />
                </SvgIcon>
              }
            />
            <NumberLabel number={serversNumber} />
            <Link className="servers-link" to="/system/explorer">Servers</Link>
          </div>

          <div>
            <Button
              startIcon={<AddIcon />}
              className="action-btn"
              aria-controls="simple-menu"
              aria-haspopup="true"
              onClick={handleClick}
            >
              Add connection
            </Button>
            <Menu
              id="connections-menu"
              keepMounted
              anchorEl={anchorElement}
              open={Boolean(anchorElement)}
              onClose={handleClose}
            >
              <MenuItem onClick={handleClose} onKeyDown={event => event.stopPropagation()}>
                <AddServerCard />
              </MenuItem>
              <MenuItem onClick={handleClose} onKeyDown={event => event.stopPropagation()}>
                <AddDatabaseCard />
              </MenuItem>
              <MenuItem onClick={handleClose} onKeyDown={event => event.stopPropagation()}>
                <ImportCSV />
              </MenuItem>
            </Menu>
          </div>
        </header>
        <div className="servers-list">
          {dbms.map(el => (
            <>
              {el.servers.map(server => (
                <div className="server-item">
                  <div className="icon-container">
                    <SvgIcon>
                      <Icon path={mdiDns} />
                    </SvgIcon>
                  </div>
                  <div className="name-container">
                    <span className="name">
                      <span className="dbms">{el.name}</span>
                      <span className="separator">/</span>
                      <span className="server">{server.address}</span>
                    </span>
                  </div>
                </div>
              ))}
            </>
          ))}
        </div>
      </Switch.Case>
    </Switch>
  );
};

export const ConnectedServersList = connect(
  state => {
    return {
      loading: state.explorer.flags.serversUsage.loading,
      dbms: state.explorer.structured?.dbms || []
    };
  },
  dispatch => ({
    getAllAccounts: () => dispatch(loadAllAccountsThunk({ from: 'dashboard' }))
  })
)(ServersList);
