import React from 'react';
import CircularProgress from '@material-ui/core/CircularProgress';
import { Center, LightTooltip, Table } from '../../../../shared/components';
import PropTypes from 'prop-types';
import SvgIcon from '@material-ui/core/SvgIcon';
import Icon from '@mdi/react';
import { mdiDelete } from '@mdi/js';
import { ConfirmationModal, ErrorModal } from '../../../../shared/components/overlays';
import { invocationOutputPoller, postInvocation } from '../../../../shared/api/invocation';

import '../Sessions.local.scss';
import './MssqlSessionsTable.local.scss';

const columns = [
  { label: 'SPID', sortKey: 'spid' },
  { label: 'ECID' },
  { label: 'Status' },
  { label: 'Login Name' },
  { label: 'Host Name' },
  { label: 'blk' },
  { label: 'DB Name' },
  { label: 'CMD' },
  { label: 'Request ID' },
  { label: 'Kill Session' },
];

const CellWithTooltip = ({ name }) => {
  if (name && name.length > 16) {
    return (
      <LightTooltip placement="bottom" title={<span>{name}</span>}>
        <span className="session-cell ellipsis">{name}</span>
      </LightTooltip>
    );
  }
  return <span className="session-cell">{name}</span>;
};

const rowRenderer = (row, index, killSession) => (
  <Table.Row hover key={`${row.sid}_${index}`} className="session-row">
    <Table.Cell>
      <CellWithTooltip name={row.spid} />
    </Table.Cell>
    <Table.Cell>
      <CellWithTooltip name={row.ecid} />
    </Table.Cell>
    <Table.Cell>
      <CellWithTooltip name={row.status} />
    </Table.Cell>
    <Table.Cell>
      <CellWithTooltip name={row.loginame} />
    </Table.Cell>
    <Table.Cell>
      <CellWithTooltip name={row.hostname} />
    </Table.Cell>
    <Table.Cell>
      <CellWithTooltip name={row.blk} />
    </Table.Cell>
    <Table.Cell>
      <CellWithTooltip name={row.dbname} />
    </Table.Cell>
    <Table.Cell>
      <CellWithTooltip name={row.cmd} />
    </Table.Cell>
    <Table.Cell>
      <CellWithTooltip name={row.request_id} />
    </Table.Cell>
    <Table.Cell>
      <div className="icon-cell">
        {(row.hostname || '').trim().length > 0 && parseInt(row.spid, 10) >= 50 && (
          <SvgIcon onClick={() => killSession(row.spid)}>
            <Icon path={mdiDelete} />
          </SvgIcon>
        )}
      </div>
    </Table.Cell>
  </Table.Row>
);

const TableRenderer = ({ isLoading, sessions, noDataMessage, tableProps, openKillSessionConfirmation }) => {
  if (isLoading) {
    return (
      <Center>
        <CircularProgress />
      </Center>
    );
  }
  if (sessions.length === 0) {
    return <Center>{noDataMessage}</Center>;
  }
  return <Table {...tableProps}>{sessions.map((row, index) => rowRenderer(row, index, (sessionId) => openKillSessionConfirmation(sessionId)))}</Table>;
};

export class MssqlSessionsTable extends React.PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      sortBehavior: { sortKey: 'spid', order: 'asc' },
      killSessionConfirmationOpen: false,
      sessionIdToKill: null
    };

    this.killSessionConfirmationActions = {
      open: (sessionId) => {
        this.setState({ killSessionConfirmationOpen: true, sessionIdToKill: sessionId });
      },
      close: () => {
        this.setState({ killSessionConfirmationOpen: false, sessionIdToKill: null });
      },
      confirm: () => {
        const { sessionIdToKill } = this.state;
        this.props.enableLoadingFlag();
        this.killSession(sessionIdToKill).then((result) => {
          if (result) {
            this.props.refreshSessions();
          } else {
            this.props.disableLoadingFlag();
            this.killSessionErrorModalActions.open();
          }
        });
      }
    };

    this.killSessionErrorModalActions = {
      open: () => {
        this.setState({ killSessionErrorOpen: true });
      },
      close: () => {
        this.setState({ killSessionErrorOpen: false });
      }
    };
  }

  render() {
    const {
      sessions,
      isLoading,
    } = this.props;

    const { sortBehavior, killSessionConfirmationOpen, killSessionErrorOpen } = this.state;
    const tableProps = { columns, sortBehavior, changeSort: this.changeSort };
    const sortedSessions = this.sortSessions(sessions);

    return (
      <>
        <TableRenderer
          isLoading={isLoading}
          sessions={sortedSessions}
          tableProps={tableProps}
          openKillSessionConfirmation={(sessionId) => this.killSessionConfirmationActions.open(sessionId)}
          noDataMessage="No Sessions Available"
        />
        <ConfirmationModal open={killSessionConfirmationOpen} actions={this.killSessionConfirmationActions} message="Are you sure you want to kill this session?" />
        <ErrorModal open={killSessionErrorOpen} actions={this.killSessionErrorModalActions} message="Failed to kill this session. Please refresh the page and try again." />
      </>
    );
  }

  changeSort = (sortKey, order) => () => {
    this.setState({ sortBehavior: { sortKey, order } });
  };

  sortSessions = (sessionsData) => {
    const { sortBehavior } = this.state;
    return (sessionsData || []).slice().sort((o1, o2) => {
      const orderMultiplier = sortBehavior.order === 'asc' ? 1 : -1;
      return (o1.spid - o2.spid) * orderMultiplier;
    });
  };

  killSession = async (sessionId) => {
    const { dbms } = this.props;
    const payload = {
      parameters: {
        session_id: sessionId,
        dbconn: dbms,
        oformat: 'JSON',
        compact: true
      },
      process: 'dbatools.mssql.security.session_kill',
      timestamp: null
    };

    const creationResult = await postInvocation(payload);
    if (creationResult.invocations.length) {
      const invocationUuid = creationResult.invocations[0].uuid;
      return invocationOutputPoller(invocationUuid);
    }
    return Promise.resolve(null);
  };
}

MssqlSessionsTable.propTypes = {
  isLoading: PropTypes.bool,
  sessions: PropTypes.array,
  dbms: PropTypes.string,
  refreshSessions: PropTypes.func,
  enableLoadingFlag: PropTypes.func,
  disableLoadingFlag: PropTypes.func,
};
