import React, { useState, useEffect, useRef } from 'react';
import { connect } from 'react-redux';
import { Modal, ModalHeader, ConfirmationModal } from 'shared/components/overlays';
import { Paper, Center } from 'shared/components/layout';
import { loadAllAccountsThunk } from '../../store/thunks';
import { DatabaseForm } from '../Forms/DatabaseForm';
import { mdiSettings, mdiDatabase, mdiDelete } from '@mdi/js';
import IconButton from '@material-ui/core/IconButton';
import SvgIcon from '@material-ui/core/SvgIcon';
import Icon from '@mdi/react';
import { Switch } from 'shared/components';
import { Loader } from 'shared/components/loaders';
import { fetchProtocols } from 'shared/api/protocols';
import { fetchDataTypes } from 'shared/api/datatypes';
import { deleteAccount } from 'shared/api/accounts';
import { ErrorModal } from '../../../shared/components/overlays';

export const DatabaseSettings = ({ database, disabled, reloadAccounts, editDbTrigger, deleteDbTrigger }) => {
  const [open, setOpen] = useState(false);
  const [deleteDbOpen, setDeleteDbOpen] = useState(false);
  const [deleteDbErrorOpen, setDeleteDbErrorOpen] = useState(false);

  const modalActions = {
    open: () => {
      setOpen(true);
    },
    close: () => {
      setOpen(false);
    }
  };

  const deleteDbModalActions = {
    open: () => {
      setDeleteDbOpen(true);
    },
    close: () => {
      setDeleteDbOpen(false);
    },
    confirm: () => {
      deleteAccount(database.name).then((res) => {
        if (res.result === 'success') {
          reloadAccounts();
        } else {
          deleteDbErrorModalActions.open();
        }
      });
    }
  };

  const deleteDbErrorModalActions = {
    open: () => {
      setDeleteDbErrorOpen(true);
    },
    close: () => {
      setDeleteDbErrorOpen(false);
    }
  };

  if (disabled && !editDbTrigger && !deleteDbTrigger) {
    return (
      <SvgIcon style={{ color: 'rgba(0, 0, 0, 0.26)' }}>
        <Icon path={mdiSettings} />
      </SvgIcon>
    );
  }

  return (
    <>
      {editDbTrigger && editDbTrigger(modalActions.open, disabled)}
      {!editDbTrigger && (
        <IconButton onClick={modalActions.open}>
          <SvgIcon>
            <Icon path={mdiSettings} />
          </SvgIcon>
        </IconButton>
      )}
      {deleteDbTrigger && deleteDbTrigger(deleteDbModalActions.open, disabled)}
      {!deleteDbTrigger && (
        <IconButton onClick={deleteDbModalActions.open}>
          <SvgIcon>
            <Icon path={mdiDelete} />
          </SvgIcon>
        </IconButton>
      )}

      <Modal open={open} actions={modalActions}>
        <EditDatabaseContainer database={database} />
      </Modal>

      <ConfirmationModal open={deleteDbOpen} actions={deleteDbModalActions} message="Are you sure you want to delete database?" />
      <ErrorModal open={deleteDbErrorOpen} actions={deleteDbErrorModalActions} message="Failed to delete Database. Please refresh the page and try again." />
    </>
  );
};

export const EditDatabase = ({ database, reloadAccounts }) => {
  const [state, setState] = useState({ loading: true, protocols: [], modes: [] });
  const formRef = useRef();
  useEffect(() => {
    // result and protocols provided by fetch
    Promise.all([
      fetchProtocols(),
      fetchDataTypes('mas0300.sys.account.privilege_mode', true)
    ]).then(([pr, mr]) => {
      if (pr.result === 'success' && mr.result === 'success') {
        setState({
          loading: false,
          result: 'success',
          protocols: pr.protocols,
          modes: mr.data_types[0]?.enumerations
        });
      } else {
        setState({
          loading: false,
          result: 'fail',
          protocols: [],
          modes: []
        });
      }
    });
  }, []);

  return (
    <Paper
      style={{
        width: '75vw',
        height: '75vh'
      }}
    >
      <ModalHeader
        icon={
          <SvgIcon>
            <path d={mdiDatabase} />
          </SvgIcon>
        }
        title="Edit Database"
        desc="Edit the database connection information"
        formikRef={formRef}
      />
      <Switch value={state.loading}>
        <Switch.Case value>
          <Center>
            <Loader />
          </Center>
        </Switch.Case>
        <Switch.Case value={false}>
          <DatabaseForm
            account={database}
            protocols={state.protocols}
            modes={state.modes}
            handleSubmission={reloadAccounts}
            editing
            formRef={formRef}
          />
        </Switch.Case>
      </Switch>
    </Paper>
  );
};

export const EditDatabaseContainer = connect(null, dispatch => ({
  reloadAccounts: () => dispatch(loadAllAccountsThunk())
}))(EditDatabase);
