const DAY = 86400000;
const HOUR = 3600000;
const MINUTE = 60000;
const SECOND = 1000;

export const formatTimePeriod = (timeMillis) => {
  let millisLeft = timeMillis;
  const days = millisLeft / DAY;
  millisLeft %= DAY;
  const hours = millisLeft / HOUR;
  millisLeft %= HOUR;
  const minutes = millisLeft / MINUTE;
  millisLeft %= MINUTE;
  const seconds = millisLeft / SECOND;
  millisLeft %= SECOND;

  return [
    days >= 1 ? `${Math.floor(days)}d` : null,
    hours >= 1 ? `${Math.floor(hours)}h` : null,
    minutes >= 1 ? `${Math.floor(minutes)}m` : null,
    seconds >= 1 ? `${Math.floor(seconds)}s` : null,
    millisLeft >= 1 ? `${Math.floor(millisLeft)}ms` : null
  ].filter(Boolean).join(' ');
};
