import React from 'react';
import { Button } from '@material-ui/core';
import { Paper } from 'shared/components/layout';
import CircularProgress from '@material-ui/core/CircularProgress';
import { Center } from 'shared/components';
import { useSelector } from 'react-redux';
import { getInvocation } from '../../store/selectors';
import { InvocationStatus } from '../../store/constants';
import { ListOutput } from '../../components/ListOutput/ListOutput';
import { fetchInvocationOutput } from 'shared/api/invocation';
import { sendToEmail } from 'license-collection/utils/sendToEmail';
import { copyToClipboard } from 'license-collection/utils/copyToClipboard';
import { ErrorModal } from '../../../shared/components/overlays';
import './output.scss';

function noOutput(invocation = {}) {
  return [
    {
      data: {
        text: 'No output to display.\n'
      }
    },
    {
      data: {
        text: `Status: ${invocation.status}\n`
      }
    },
    {
      data: {
        text: `Process: ${invocation.process}\n`
      }
    },
    {
      data: {
        text: `Invocation: ${invocation.uuid}\n`
      }
    }
  ];
}

function onSendByEmail(invocation, output, processName, modalActions) {
  const result = sendToEmail(invocation, output, processName);
  if (!result) {
    modalActions.open();
  }
}

export const JobOutput = () => {
  const invocation = useSelector(getInvocation);

  const [loading, setLoading] = React.useState(true);
  const [output, setOutput] = React.useState(noOutput(invocation));
  const [sendToEmailWarningOpen, setSendToEmailWarningOpen] = React.useState(false);
  const modalActions = {
    open: () => {
      setSendToEmailWarningOpen(true);
    },
    close: () => {
      setSendToEmailWarningOpen(false);
    }
  };
  const sendToEmailWarning = 'The output is too long and can\'t be opened in your email client. Please use "COPY TO CLIPBOARD" button and paste it in your email.';

  React.useEffect(() => {
    fetchInvocationOutput(invocation.uuid)
      .then(({ data }) => {
        const recieved = data.data.outputs;

        if (recieved && recieved.length) {
          setOutput(recieved);
        } else {
          setOutput(noOutput(invocation));
        }

        setLoading(false);
      })
      .catch(() => {
        setOutput(noOutput(invocation));
        setLoading(false);
      });
  }, []);

  if (loading) {
    return (
      <Center>
        <CircularProgress />
      </Center>
    );
  }

  const outputType = invocation.status === InvocationStatus.FAILED ? 'fail' : 'normal';

  const monitoring = output?.[0]?.data?.text?.match?.(
    /{"process": {"uuid": "([0-9a-f]{8}\b-[0-9a-f]{4}-[0-9a-f]{4}-[0-9a-f]{4}-\b[0-9a-f]{12})",/
  )?.[1];

  console.log(monitoring); // eslint-disable-line

  return (
    <>
      <Paper.Content scrollable>
        <ListOutput
          type={outputType}
          output={output}
          outputFormat={invocation.parameters.oformat}
          errors={invocation.errors}
          monitoring={monitoring}
        />
      </Paper.Content>
      <Paper.Footer>
        <Paper.Footer.Left>
          <Button
            color="primary"
            onClick={() => onSendByEmail(invocation, output, invocation.process, modalActions)}
          >
            Send to email
          </Button>
          <Button color="primary" onClick={() => copyToClipboard(output, invocation)}>
            Copy to clipboard
          </Button>
        </Paper.Footer.Left>
      </Paper.Footer>
      <ErrorModal open={sendToEmailWarningOpen} actions={modalActions} message={sendToEmailWarning} />
    </>
  );
};
