import React from 'react';
import { Bar, BarChart, CartesianGrid, Legend, ResponsiveContainer, Tooltip, XAxis, YAxis } from 'recharts';
import { formatTimePeriod } from '../../../../license-collection/utils/formatTimePeriod';
import './ElapsedTimeChart.local.scss';

const renderCustomXAxisTick = ({ x, y, payload }) => {
  return (
    <g transform={`translate(${x},${y})`}>
      <text className="label" x={0} y={0} dy={20} textAnchor="end" fill="#666" transform="rotate(-80)">{payload.value}</text>
    </g>
  );
};

const renderLegend = ({ payload }) => {
  return (
    <ul className="elapsed-time-chart-legend">
      {
        (payload || []).map((entry, index) => (
          <li className="legend-item" key={`item-${index}`}>
            <span className="icon" style={{ backgroundColor: entry.color }} />
            <span className="label" style={{ color: entry.color }}>{entry.value}</span>
          </li>
        ))
      }
    </ul>
  );
};

const renderChartTooltip = (data) => {
  if (!data || !data.payload || !data.payload.length) {
    return '';
  }

  const [elapsedTimeData] = data.payload;
  const { sqlId, timeMillisFull, cpuPercent, cpuTime, ioPercent, ioTime } = elapsedTimeData.payload;
  return (
    <div className="elapsed-time-chart-tooltip">
      <div className="row"><strong className="label">SQL ID: </strong><span className="value">{sqlId}</span></div>
      <div className="row"><strong className="label">Elapsed Time: </strong><span className="value">{formatTimePeriod(timeMillisFull)}</span></div>
      <div className="row"><strong className="label">CPU: </strong><span className="value">{cpuPercent}% ({formatTimePeriod(cpuTime)})</span></div>
      <div className="row"><strong className="label">I/O: </strong><span className="value">{ioPercent}% {ioPercent > 0 ? `(${formatTimePeriod(ioTime)})` : ''}</span></div>
    </div>
  );
};

export const ElapsedTimeChart = ({ data }) => {
  return (
    <ResponsiveContainer>
      <BarChart data={data} margin={{ top: 50, right: 30, left: 70, bottom: 100 }} barGap={-10}>
        <CartesianGrid strokeDasharray="3 3" vertical={false} />
        <Legend content={renderLegend} align="right" verticalAlign="top" layout="vertical" width={200} />
        <Tooltip content={renderChartTooltip} cursor={{ fill: '#eeeeee' }} />

        <XAxis dataKey="sqlId" axisLine="false" interval={0} tick={renderCustomXAxisTick} />
        <YAxis dataKey="timeMillisFull" domain={['auto', 'auto']} tickFormatter={tick => formatTimePeriod(tick)} />

        <Bar dataKey="cpuTime" stackId="common" name="CPU, %" fill="#1ac9e6" />
        <Bar dataKey="ioTime" stackId="common" name="I/O, %" fill="#c02323" />
        <Bar dataKey="timeMillis" stackId="common" name="Elapsed Time" fill="#176BA0" />
      </BarChart>
    </ResponsiveContainer>
  );
};
