import React from 'react';
import { LightTooltip, Table } from '../../../shared/components';
import { array, func } from 'prop-types';
import { Paper } from '../../../shared/components/layout';
import Icon from '@mdi/react';
import {
  mdiChartTimelineVariant,
  mdiCheckboxMultipleBlankOutline,
  mdiFormatAlignTop,
  mdiHexagonSlice2,
  mdiLicense,
  mdiShape
} from '@mdi/js';
import SvgIcon from '@material-ui/core/SvgIcon';
import Checkbox from '@material-ui/core/Checkbox';
import { connect } from 'react-redux';
import { getDBStatus, getDBStatusInfo, getEntityPendingStatus, getServerStatus } from '../../store/selectors';
import { StatusLabel } from '../StatusLabel/StatusLabel';
import { changeStateThunk, loadAllAccountsThunk, refreshDBStatusThunk } from '../../store/thunks';
import { Button } from 'shared/components/controls';
import { SystemExplorerFeatureUtil } from '../../SystemExplorerFeatureUtil';
import Refresh from '@material-ui/icons/Refresh';
import { Loader } from '../../../shared/components/loaders';
import { AddServerCard } from '../../containers/AddServerCard/AddServerCard';
import { AddDatabaseCard } from '../../containers/AddDatabaseCard/AddDatabaseCard';
import { ImportCSV } from '../../containers/ImportCSV/ImportCSV';
import { DatabaseSettings } from '../../containers/Settings/DatabaseSettings';
import { CONSTANTS } from 'config';

import './SystemExplorerTable.local.scss';

const HostnameCellWithTooltip = ({ name }) => {
  if (name && name.length > 15) {
    return (
      <LightTooltip placement="bottom" title={<span>{name}</span>}>
        <span className="hostname-cell ellipsis">{name}</span>
      </LightTooltip>
    );
  }
  return <span className="hostname-cell">{name}</span>;
};

const columns = [
  { label: 'Hostname' },
  { label: 'Server' },
  { label: 'Port', align: 'center' },
  { label: 'User', align: 'center' },
  { label: 'Server Status', align: 'center' },
  { label: 'Database Status', align: 'center' },
  { label: 'Workload', align: 'center' },
  { label: 'Session', align: 'center' },
  { label: 'Top SQL', align: 'center' },
  { label: 'Objects', align: 'center' },
  { label: 'Storage', align: 'center' },
];

const rowRenderer = (database, index, isChecked, updateSelection, retrieveDbStatus, retrieveServerStatus, retrievePendingStatus, navigateToDetails) => (
  <Table.Row hover key={`${database.name}_${index}`} className="statement-row">
    <Table.Cell padding="checkbox">
      <Checkbox
        onChange={(event) => updateSelection(database, event.target.checked)}
        checked={isChecked}
        size="small"
        color="primary"
        classes={{ root: 'checkbox-custom' }}
      />
    </Table.Cell>
    <Table.Cell padding="none" className="host-cell">
      <HostnameCellWithTooltip name={SystemExplorerFeatureUtil.splitAccountName(database.name)} />
    </Table.Cell>
    <Table.Cell className="address-cell">
      {database.address}
    </Table.Cell>
    <Table.Cell>
      <div className="centered-cell">{database.port}</div>
    </Table.Cell>
    <Table.Cell className="user-cell">
      <div className="centered-cell">{database.user}</div>
    </Table.Cell>
    <Table.Cell className="status-cell">
      <div className="centered-cell">
        {!retrievePendingStatus(database).loading && (
          <StatusLabel status={retrieveServerStatus(database)} />
        )}
        {retrievePendingStatus(database).loading && (
          <Loader size="20px" />
        )}
      </div>
    </Table.Cell>
    <Table.Cell className="status-cell">
      <div className="centered-cell">
        {!retrievePendingStatus(database).loading && (
          <StatusLabel status={retrieveDbStatus(database)} />
        )}
        {retrievePendingStatus(database).loading && (
          <Loader size="20px" />
        )}
      </div>
    </Table.Cell>
    <Table.Cell>
      <div className={`icon-cell ${!SystemExplorerFeatureUtil.isWorkloadAllowed(database.protocol) ? 'icon-disabled' : ''}`}>
        <SvgIcon onClick={() => navigateToDetails({ path: database.path, level: 'workload' })}>
          <Icon path={mdiChartTimelineVariant} />
        </SvgIcon>
      </div>
    </Table.Cell>
    <Table.Cell>
      <div className={`icon-cell ${!SystemExplorerFeatureUtil.isSessionsAllowed(database.protocol) ? 'icon-disabled' : ''}`}>
        <SvgIcon onClick={() => navigateToDetails({ path: database.path, level: 'sessions' })}>
          <Icon path={mdiCheckboxMultipleBlankOutline} />
        </SvgIcon>
      </div>
    </Table.Cell>
    <Table.Cell>
      <div className={`icon-cell ${!SystemExplorerFeatureUtil.isTopSqlAllowed(database.protocol) ? 'icon-disabled' : ''}`}>
        <SvgIcon onClick={() => navigateToDetails({ path: database.path, level: 'topsql' })}>
          <Icon path={mdiFormatAlignTop} />
        </SvgIcon>
      </div>
    </Table.Cell>
    <Table.Cell>
      <div className={`icon-cell ${!SystemExplorerFeatureUtil.isObjectsAllowed(database.protocol) ? 'icon-disabled' : ''}`}>
        <SvgIcon onClick={() => navigateToDetails({ path: database.path, level: 'objects' })}>
          <Icon path={mdiShape} />
        </SvgIcon>
      </div>
    </Table.Cell>
    <Table.Cell>
      <div className={`icon-cell ${!SystemExplorerFeatureUtil.isStorageAllowed(database.protocol) ? 'icon-disabled' : ''}`}>
        <SvgIcon onClick={() => navigateToDetails({ path: database.path, level: 'storage' })}>
          <Icon path={mdiHexagonSlice2} />
        </SvgIcon>
      </div>
    </Table.Cell>
    {SystemExplorerFeatureUtil.isLicenseReportingAllowed(database.protocol) && (
      <Table.Cell>
        <div className="icon-cell">
          <SvgIcon onClick={() => navigateToDetails({ path: database.path, level: 'license' })}>
            <Icon path={mdiLicense} />
          </SvgIcon>
        </div>
      </Table.Cell>
    )}
  </Table.Row>
);

const testConnectionsTrigger = (clickHandler, isDisabled) => {
  return (
    <Button
      variant="contained"
      color="primary"
      size="small"
      onClick={clickHandler}
      startIcon={<Refresh />}
      classes={{ root: 'green-btn toolbar-btn' }}
      disabled={isDisabled}
    >
      Test Connection
    </Button>
  );
};

const addServerTrigger = (clickHandler) => {
  return (
    <Button
      variant="contained"
      color="primary"
      size="small"
      classes={{ root: 'green-btn toolbar-btn' }}
      onClick={clickHandler}
      disabled={CONSTANTS.NEW_CUSTOMER_POC_ENABLED}
    >
      Add New Server
    </Button>
  );
};

const addDatabaseTrigger = (clickHandler) => {
  return (
    <Button
      variant="contained"
      color="primary"
      size="small"
      classes={{ root: 'green-btn toolbar-btn' }}
      onClick={clickHandler}
      disabled={CONSTANTS.NEW_CUSTOMER_POC_ENABLED}
    >
      Add New Database
    </Button>
  );
};

const importCsvTrigger = (clickHandler) => {
  return (
    <Button
      variant="contained"
      color="primary"
      size="small"
      classes={{ root: 'toolbar-btn' }}
      onClick={clickHandler}
      disabled={CONSTANTS.NEW_CUSTOMER_POC_ENABLED}
    >
      Import from CSV
    </Button>
  );
};

const editDatabaseTrigger = (clickHandler, isDisabled) => {
  return (
    <Button
      variant="contained"
      color="primary"
      size="small"
      classes={{ root: 'yellow-btn toolbar-btn' }}
      onClick={clickHandler}
      disabled={isDisabled || CONSTANTS.NEW_CUSTOMER_POC_ENABLED}
    >
      Edit Database
    </Button>
  );
};

const deleteDatabaseTrigger = (clickHandler, isDisabled) => {
  return (
    <Button
      variant="contained"
      color="primary"
      size="small"
      classes={{ root: 'red-btn toolbar-btn' }}
      onClick={clickHandler}
      disabled={isDisabled || CONSTANTS.NEW_CUSTOMER_POC_ENABLED}
    >
      Delete Database
    </Button>
  );
};

class SystemExplorerTable extends React.PureComponent {
  allGroups = [];

  allDatabases = [];

  constructor(props) {
    super(props);
    this.state = {
      selectedDbs: []
    };

    const { data } = this.props;
    this.allGroups =
      data.filter(protocolGroup => protocolGroup.databases && protocolGroup.databases.length)
          .sort((group1, group2) => {
            return SystemExplorerFeatureUtil.getListOrder(group2.protocol) - SystemExplorerFeatureUtil.getListOrder(group1.protocol);
          });

    this.allDatabases = this.allGroups.reduce((acc, protocolGroup) => {
      acc = acc.concat(protocolGroup.databases);
      return acc;
    }, []);
  }

  render() {
    const {
      switchToTileView,
      retrieveDbStatus,
      retrieveServerStatus,
      retrievePendingStatus,
      reloadAccounts,
      changeState
    } = this.props;
    const { selectedDbs } = this.state;
    const navigateToDetails = (detailsPageParams) => {
      changeState(detailsPageParams);
      switchToTileView();
    };

    return (
      <Paper>
        <Paper.Header>
          <div className="system-explorer-table-view">
            <div className="system-explorer-table-toolbar">
              {testConnectionsTrigger(this.testConnections, !selectedDbs.length)}
              <AddServerCard trigger={addServerTrigger} />
              <AddDatabaseCard trigger={addDatabaseTrigger} />
              <ImportCSV trigger={importCsvTrigger} />
              <DatabaseSettings
                database={this.getFirstDatabase()}
                disabled={!this.hasSingleSelection()}
                editDbTrigger={editDatabaseTrigger}
                deleteDbTrigger={deleteDatabaseTrigger}
                reloadAccounts={reloadAccounts}
              />
            </div>
          </div>
        </Paper.Header>
        <Paper.Content>
          <div className="system-explorer-table-content">
            {this.allGroups.map(protocolGroup => {
              const columnsConfig = JSON.parse(JSON.stringify(columns));
              columnsConfig.unshift({ component: this.createCheckboxHeaderColumnConfig(protocolGroup), padding: 'checkbox' });
              if (SystemExplorerFeatureUtil.isLicenseReportingAllowed(protocolGroup.protocol)) {
                columnsConfig.push({ label: 'License Reporting', align: 'center' });
              }
              const tableProps = { columns: columnsConfig };
              return (
                <>
                  <h3 className="protocol-header">{protocolGroup.protocol}</h3>
                  <Table {...tableProps} className="table">
                    {(protocolGroup.databases || []).map((db, index) =>
                      rowRenderer(db, index, this.isDbSelected(db), this.updateSelection, retrieveDbStatus, retrieveServerStatus, retrievePendingStatus, navigateToDetails))}
                  </Table>
                </>
              );
            })}
          </div>
        </Paper.Content>
      </Paper>
    );
  }

  createCheckboxHeaderColumnConfig = (protocolGroup) => {
    return () => {
      return (
        <Checkbox
          size="small"
          color="primary"
          onChange={(event) => this.toggleDbsSelectionByProtocol(event, protocolGroup)}
          checked={this.isAllDbsByProtocolSelected(protocolGroup.protocol)}
          classes={{ root: 'checkbox-custom' }}
        />
      );
    };
  };

  updateSelection = (database, isChecked) => {
    const { selectedDbs } = this.state;
    const dbIndex = selectedDbs.findIndex((db) => db.name === database.name);

    if (isChecked && dbIndex === -1) {
      selectedDbs.push(database);
    } else if (dbIndex !== -1) {
      selectedDbs.splice(dbIndex, 1);
    }

    this.setState({ selectedDbs });
    this.forceUpdate();
  };

  toggleDbsSelectionByProtocol = (event, protocolGroup) => {
    const shouldAddAll = !this.isAllDbsByProtocolSelected(protocolGroup.protocol);
    const { selectedDbs } = this.state;
    const allWithoutCurrent = this.clearDbsByProtocol(selectedDbs, protocolGroup.protocol);

    if (shouldAddAll) {
     this.setState({ selectedDbs: [...allWithoutCurrent, ...protocolGroup.databases] });
    } else {
      this.setState({ selectedDbs: [...allWithoutCurrent] });
    }
    this.forceUpdate();
  };

  clearDbsByProtocol = (dbs, protocol) => {
    return dbs.filter((db) => db.protocol !== protocol);
  };

  isAllDbsByProtocolSelected = (protocol) => {
    const allByProtocol = this.allDatabases.filter((db) => db.protocol === protocol);
    const selectedByProtocol = (this.state.selectedDbs || []).filter((db) => db.protocol === protocol);
    return allByProtocol.length === selectedByProtocol.length;
  }

  testConnections = () => {
    const { selectedDbs } = this.state;
    const { refreshDatabaseStatus } = this.props;
    (selectedDbs || []).forEach(db => refreshDatabaseStatus(db));
  };

  isDbSelected = (db) => {
    const { selectedDbs } = this.state;
    return selectedDbs.some((selected) => selected.name === db.name);
  };

  hasSingleSelection = () => {
    const { selectedDbs } = this.state;
    return !!selectedDbs && selectedDbs.length === 1;
  };

  getFirstDatabase = () => {
    const { selectedDbs } = this.state;
    return this.hasSingleSelection() ? selectedDbs[0] : null;
  };
}

SystemExplorerTable.propTypes = {
  data: array,
  switchToTileView: func
};

export const ConnectedSystemExplorerTable = connect(
  state => {
    const statuses = state.explorer.dbStatus;

    return {
      statuses,
      retrieveServerStatus: server => getServerStatus(server, statuses),
      retrieveDbStatus: database => getDBStatus(database, statuses),
      retrieveDbStatusInfo: database => getDBStatusInfo(database, statuses),
      retrievePendingStatus: database => getEntityPendingStatus(database, state.explorer.dbStatus, 'db')
    };
  },
  dispatch => ({
    changeState: newState => dispatch(changeStateThunk(newState)),
    refreshDatabaseStatus: db => dispatch(refreshDBStatusThunk(db)),
    reloadAccounts: () => dispatch(loadAllAccountsThunk())
  })
)(SystemExplorerTable);
