import React from 'react';
import { any, func } from 'prop-types';
import { Center } from 'shared/components/layout';
import './job-list.local.scss';
import { connect } from 'react-redux';
import { currentUserSelector } from 'session/store/state';

const JobList = ({ items, filters, children, loggedUser }) => {
  const noneAvailable = items.size === 0;

  if (noneAvailable) {
    return <NoJobs />;
  }

  return (
    <div className="list-container">
      <ul>
        {items.map((item, index, list) => (
          <li key={index} className="list-item">
            {children(item, index, filters, loggedUser, list)}
          </li>
        ))}
      </ul>
    </div>
  );
};

JobList.propTypes = {
  items: any,
  children: func
};

JobList.defaultProps = {
  items: []
};

function NoJobs() {
  return (
    <Center>
      <p>There are no jobs available with the selected filters</p>
    </Center>
  );
}

export const ConnectedJobList = connect(
  state => {
    return {
      loggedUser: currentUserSelector(state),
    };
  }
)(JobList);
