import React from 'react';
import classnames from 'classnames';
import { LightTooltip } from 'shared/components/tooltip/Tooltip';
import SvgIcon from '@material-ui/core/SvgIcon';
import Icon from '@mdi/react';
import { mdiTable, mdiViewComfy } from '@mdi/js';
import { SystemExplorerViewMode } from './SystemExplorerViewMode';

import './ToggleSystemExplorerView.local.scss';

export const ToggleSystemExplorerView = ({ active, onClickCallback }) => {
  return (
    <div className="toggle-container">
      <LightTooltip placement="bottom" title="Table View">
        <div
          className={classnames('option', {
            active: active === SystemExplorerViewMode.TABLE
          })}
          onClick={() => onClickCallback(SystemExplorerViewMode.TABLE)}
          role="presentation"
        >
          <SvgIcon>
            <Icon path={mdiTable} />
          </SvgIcon>
        </div>
      </LightTooltip>
      <LightTooltip placement="bottom" title="Tiles View">
        <div
          className={classnames('option', {
            active: active === SystemExplorerViewMode.TILES
          })}
          onClick={() => onClickCallback(SystemExplorerViewMode.TILES)}
          role="presentation"
        >
          <SvgIcon>
            <Icon path={mdiViewComfy} />
          </SvgIcon>
        </div>
      </LightTooltip>
    </div>
  );
};
