const MONTH_LIST = ['jan', 'feb', 'mar', 'apr', 'may', 'jun', 'jul', 'aug', 'sep', 'oct', 'nov', 'dec'];
const WEEKDAYS_LIST = ['sun', 'mon', 'tue', 'wed', 'thu', 'fri', 'sat'];
const MAX_MINUTES_VALUE = 59;
const MAX_HOUR_VALUE = 23;
const MAX_DAY_VALUE = 31;
const MAX_MONTH_VALUE = 12;
const MAX_WEEKDAY_VALUE = 7;

export class CronValidator {
  // public methods
  static isValidMinutes(minutes) {
    return CronValidator.isValidNumberValue(CronValidator.cleanValue(minutes), MAX_MINUTES_VALUE);
  }

  static isValidHours(hours) {
    return CronValidator.isValidNumberValue(CronValidator.cleanValue(hours), MAX_HOUR_VALUE);
  }

  static isValidDays(days) {
    return CronValidator.isValidNumberValue(CronValidator.cleanValue(days), MAX_DAY_VALUE);
  }

  static isValidMonth(month) {
    return CronValidator.isValidNumberValue(CronValidator.cleanValue(month), MAX_MONTH_VALUE, false)
      || CronValidator.isValidAlternativeValue(CronValidator.cleanValue(month), MONTH_LIST);
  }

  static isValidWeekDay(weekDay) {
    return CronValidator.isValidNumberValue(CronValidator.cleanValue(weekDay), MAX_WEEKDAY_VALUE)
      || CronValidator.isValidAlternativeValue(CronValidator.cleanValue(weekDay), WEEKDAYS_LIST);
  }

  static FORMAT_MSG = {
    MINUTES: 'Minute value is required',
    HOURS: 'Hour values must be between 0 and 23 and can contain special symbols: , - *',
    DAYS: 'Day values must be between 0 and 31 and can contain special symbols: , - *',
    MONTH: 'Month values must be between 1 and 12, or JAN-DEC, and can contain special symbols: , - *',
    DAY_OF_WEEK: 'Day-of-Week values must be between 0 and 7, or SUN-SAT, and can contain special symbols: , - *',
    EVERY_HOUR_WARNING: 'You have selected a process to run every hour. This may not be advisable for processes that consume significant storage resources, like backup processes.'
  };

  static ALLOWED_MINUTES_OPTIONS = ['0', '10', '20', '30', '40', '50'];

  // private methods
  static isValidNumberValue(time, maxVal, zeroAllowed = true) {
    if (time === '*' || (zeroAllowed && time === '0')) {
      return true;
    }

    if (time.includes(',')) {
      return time.split(',').every((part) => {
        return CronValidator.isValidNumberPart(part, maxVal, zeroAllowed);
      });
    }

    return CronValidator.isValidNumberPart(time, maxVal, zeroAllowed);
  }

  static isValidNumberPart(time, maxVal, zeroAllowed = true) {
    if (time.includes('/')) {
      return false;
    }
    // Uncomment if we should allow a `/` symbol for Cron schedule
    /*
    if (time.includes('/')) {
      const stepParts = time.split('/');
      if (stepParts.length !== 2) {
        return false;
      }

      const stepPartsValid = CronValidator.isValidNumberPart(stepParts[0], maxVal, zeroAllowed)
        && CronValidator.isValidNumberPart(stepParts[1], maxVal, zeroAllowed);

      return stepPartsValid || (stepParts[0] === '*' && CronValidator.isValidNumberPart([stepParts[1]], maxVal, zeroAllowed));
    }
    */

    if (time.includes('-')) {
      const rangeNumbersArr = time.split('-').map((el) => parseInt(el, 10));
      return CronValidator.isValidNumberRange(rangeNumbersArr, maxVal) && rangeNumbersArr.length === 2 && rangeNumbersArr[0] <= rangeNumbersArr[1];
    }

    const timeNumber = parseInt(time, 10);
    if (zeroAllowed) {
      return timeNumber >= 0 && timeNumber <= maxVal;
    }
    return timeNumber > 0 && timeNumber <= maxVal;
  }

  static cleanValue(value) {
    return (value || '').trim().toLowerCase();
  }

  static isValidNumberRange(dataArray, maxVal, zeroAllowed = true) {
    return dataArray.every((element) => {
      if (zeroAllowed) {
        return element >= 0 && element <= maxVal;
      }
      return element > 0 && element <= maxVal;
    });
  }

  static isValidAlternativeValue(value, allowedValues) {
    if (value.includes(',')) {
      return value.split(',').every((part) => {
        return CronValidator.isValidAlternativePart(part, allowedValues);
      });
    }

    return CronValidator.isValidAlternativePart(value, allowedValues);
  }

  static isValidAlternativePart(value, allowedValues) {
    if (value.includes('/')) {
      return false;
      /*
      const stepValues = value.split('/');
      return CronValidator.isValidAlternativeRange(stepValues, allowedValues);
      */
    }

    if (value.includes('-')) {
      const rangeValues = value.split('-');
      return CronValidator.isValidAlternativeRange(rangeValues, allowedValues) && rangeValues.length === 2
        && allowedValues.indexOf(rangeValues[0]) <= allowedValues.indexOf(rangeValues[1]);
    }

    return CronValidator.isValidAlternativeRange([value], allowedValues);
  }

  static isValidAlternativeRange(values, allowedValues) {
    return values.every((value) => {
      return allowedValues.includes(CronValidator.cleanValue(value));
    });
  }
}
