import React from 'react';
import { connect } from 'react-redux';
import { Form, Formik } from 'formik';
import { ProcessParameters } from 'job-process/job-parameters';
import { Notifications } from 'job-process/containers/notifications/notifications';
import { fetchEntity } from 'entities/actions';
import { Column, Paper, Row, Section } from 'shared/components/layout';
import {
  PLATFORMS,
  PlatformSelect,
  PROCESS_TYPES,
  ProcessSelect,
  ProcessTypeSelect
} from 'shared/components/form/editors/Custom';
import { SubmitButton } from 'shared/components/form/SubmitButton';
import { Text } from 'shared/components/form/editors/Generic';
import { patchSchedule, postSchedule } from 'shared/api/schedule';
import { mapScheduledNames } from 'entities/mappers';
import { isEmpty, pickBy } from 'lodash';
import { CronValidator } from '../../../../../shared/lib/cron-validator/cron-validator';
import { RestClient } from '../../../../../shared/lib/rest';
import { CONSTANTS } from 'config';
import moment from 'moment';

import './ScheduleForm.scss';

class ScheduleFormComp extends React.PureComponent {
  constructor(props) {
    super(props);

    const { invocation } = this.props;
    const derivedMonitor = {
      enabled: !!invocation?.parameters?.events,
      events: invocation?.parameters?.events || 0,
      users: invocation?.parameters?.users || []
    };

    this.state = {
      monitor: derivedMonitor,
      serverTime: null,
      everyHourWarningShown: false
    };
  }

  componentDidMount() {
    RestClient.get(CONSTANTS.BASE.ENDPOINTS.CURRENT_TIME).then((response) => {
      this.setState({ serverTime: response.data.data });
    });

    const { wasSCheduleCallDone, request } = this.props;
    if (!wasSCheduleCallDone) {
      request('schedule');
    }
  }

  render() {
    const {
      setResult,
      request,
      allScheduledNames,
      invocation,
      isScheduled,
      filledValues,
      setFilledValues,
      selectedOption,
      canSelectProcess,
      formRef
    } = this.props;
    const { monitor, serverTime, everyHourWarningShown } = this.state;
    const masTimeFormatted = serverTime ? moment(serverTime.currentTime).format('HH:mm MMM DD YYYY') : null;

    const derivedMonitor = {
      enabled: !!invocation?.parameters?.events,
      events: invocation?.parameters?.events || 0,
      users: invocation?.parameters?.users || []
    };

    const isEditing = selectedOption === 'edit-schedule';

    const availableProcess = (platformVal, typeVal, processVal) => {
      const selectedPlatform = PLATFORMS.find(platform => platform.value === platformVal);
      const selectedType = PROCESS_TYPES.find(type => type.value === typeVal);

      const platformMatches = !selectedPlatform || selectedPlatform.tester.test(processVal);
      const typeMatches = !selectedType || selectedType.tester.test(processVal);

      return (platformMatches && typeMatches) ? processVal : '';
    };

    const nameValue = () => {
      if (isScheduled && invocation?.name) {
        const splittedName = invocation.name.split('.');
        const name = splittedName[splittedName.length - 1];

        return name;
      }

      return invocation?.name || undefined;
    };

    const extraValidations = values => {
      const { t_min, t_hr, t_day, t_mon, t_wkd, sch_name } = values;
      const errors = {};

      if (sch_name) {
        if (sch_name.includes('.')) {
          errors.sch_name = 'Name cannot include "."';
        } else if (sch_name.includes(' ')) {
          errors.sch_name = 'Name cannot include spaces';
        } else if (allScheduledNames.includes(sch_name)) {
          if (isScheduled && invocation?.name) {
            const splittedName = invocation.name.split('.');
            const name = splittedName[splittedName.length - 1];

            if (name !== sch_name) {
              errors.sch_name = 'A schedule with this name already exists';
            }
          } else {
            errors.sch_name = 'A schedule with this name already exists';
          }
        }
      }

      if (
        values['fqname--name'] &&
        (!/^(?:[a-z_][a-z0-9_.]*)$/i.test(values['fqname--name']) ||
          values['fqname--name'].substr(-1) === '.')
      ) {
        errors['fqname--name'] = 'A valid MAS relative name is required.';
      }

      this.setState({ everyHourWarningShown: t_hr === '*' });

      if (t_hr === '*' && t_day === '*' && t_mon === '*' && t_wkd === '*') {
        errors.t_hr = 'At least one value should be populated';
        errors.t_day = 'At least one value should be populated';
        errors.t_mon = 'At least one value should be populated';
        errors.t_wkd = 'At least one value should be populated';
        return errors;
      }

      if (!CronValidator.isValidMinutes(t_min)) {
        errors.t_min = CronValidator.FORMAT_MSG.MINUTES;
      }

      if (!CronValidator.isValidHours(t_hr)) {
        errors.t_hr = CronValidator.FORMAT_MSG.HOURS;
      }

      if (!CronValidator.isValidDays(t_day)) {
        errors.t_day = CronValidator.FORMAT_MSG.DAYS;
      }

      if (!CronValidator.isValidMonth(t_mon)) {
        errors.t_mon = CronValidator.FORMAT_MSG.MONTH;
      }

      if (!CronValidator.isValidWeekDay(t_wkd)) {
        errors.t_wkd = CronValidator.FORMAT_MSG.DAY_OF_WEEK;
      }

      return errors;
    };

    return (
      <>
        <Formik
          initialValues={{}}
          validate={extraValidations}
          onSubmit={values => {
            handleSubmit(monitor, setResult, request, setFilledValues, invocation, isEditing, values);
          }}
          innerRef={formRef}
        >
          {() => {
            return (
              <>
                <Paper.Content>
                  <Form>
                    <Section>
                      <Section.Header>
                        <Section.Title>Process</Section.Title>
                      </Section.Header>
                      <Section.Content>
                        <Row>
                          {canSelectProcess && (
                            <Column xs={4}>
                              <PlatformSelect
                                name="platform"
                                label="Platform"
                                description="Select a platform to filter out the processes"
                                reactToChange={(value, formik) => {
                                  formik.setFieldValue(
                                    'process',
                                    availableProcess(value, formik.values.type, formik.values.process)
                                  );
                                }}
                              />
                            </Column>
                          )}
                          {canSelectProcess && (
                            <Column xs={4}>
                              <ProcessTypeSelect
                                name="type"
                                label="Type"
                                description="Select a type to filter out the processes"
                                reactToChange={(value, formik) => {
                                  formik.setFieldValue(
                                    'process',
                                    availableProcess(formik.values.platform, value, formik.values.process)
                                  );
                                }}
                              />
                            </Column>
                          )}
                          <Column xs={4}>
                            <ProcessSelect
                              name="process"
                              label="Process"
                              value={filledValues?.process || invocation?.process}
                              disabled={invocation !== undefined}
                              required
                            />
                          </Column>
                        </Row>
                      </Section.Content>
                    </Section>
                    <Section>
                      <Section.Header>
                        <Section.Title>Schedule</Section.Title>
                      </Section.Header>
                      <Section.Content>
                        <Row>
                          <Text
                            name="sch_name"
                            label="Name"
                            value={filledValues?.name || nameValue()}
                            required
                          />
                        </Row>
                        <Row>
                          <Text
                            name="sch_description"
                            label="Description"
                            value={filledValues?.description || invocation?.description || undefined}
                            multiline
                            required
                          />
                        </Row>
                        <Section.Title>Server time: {masTimeFormatted}</Section.Title>
                        <Row>
                          <Column style={{ maxWidth: '20%', width: '20%' }}>
                            <Text
                              name="t_min"
                              label="T_min"
                              value={filledValues?.t_min || invocation?.t_min || CronValidator.ALLOWED_MINUTES_OPTIONS[0]}
                              options={CronValidator.ALLOWED_MINUTES_OPTIONS.map(option => ({
                                value: option,
                                label: option
                              }))}
                              description={CronValidator.FORMAT_MSG.MINUTES}
                            />
                          </Column>
                          <Column style={{ maxWidth: '20%', width: '20%' }}>
                            <Text
                              name="t_hr"
                              label="T_hr"
                              value={filledValues?.t_hr || invocation?.t_hr || '*'}
                              description={CronValidator.FORMAT_MSG.HOURS}
                            />
                            {everyHourWarningShown && (
                              <div className="every-hour-warning">{CronValidator.FORMAT_MSG.EVERY_HOUR_WARNING}</div>
                            )}
                          </Column>
                          <Column style={{ maxWidth: '20%', width: '20%' }}>
                            <Text
                              name="t_day"
                              label="T_Day"
                              value={filledValues?.t_day || invocation?.t_day || '*'}
                              description={CronValidator.FORMAT_MSG.DAYS}
                            />
                          </Column>
                          <Column style={{ maxWidth: '20%', width: '20%' }}>
                            <Text
                              name="t_mon"
                              label="T_mon"
                              value={filledValues?.t_mon || invocation?.t_mon || '*'}
                              description={CronValidator.FORMAT_MSG.MONTH}
                            />
                          </Column>
                          <Column style={{ maxWidth: '20%', width: '20%' }}>
                            <Text
                              name="t_wkd"
                              label="T_wkd"
                              value={filledValues?.t_wkd || invocation?.t_wkd || '*'}
                              description={CronValidator.FORMAT_MSG.DAY_OF_WEEK}
                            />
                          </Column>
                        </Row>
                      </Section.Content>
                    </Section>
                    <ProcessParameters
                      recieved={filledValues?.parameters || invocation?.parameters}
                      editable
                    />
                    <Section>
                      <Section.Header>
                        <Section.Title>Notifications</Section.Title>
                      </Section.Header>
                      <Section.Content>
                        <Notifications
                          name="notifications"
                          recievedData={derivedMonitor}
                          onApply={n_state => {
                            if (n_state.events === 0 || isEmpty(n_state.users)) {
                              this.setState({ monitor: { enabled: false } });
                            }
                            this.setState({ monitor: { enabled: true, ...n_state } });
                          }}
                        />
                      </Section.Content>
                    </Section>
                  </Form>
                </Paper.Content>
                <Paper.Footer>
                  <Paper.Footer.Right>
                    <SubmitButton variant="contained">Save</SubmitButton>
                  </Paper.Footer.Right>
                </Paper.Footer>
              </>
            );
          }}
        </Formik>
      </>
    );
  }
}

export const ConnectedScheduleForm = connect(
  state => {
    return {
      wasSCheduleCallDone: state.entities?.data?.schedule,
      allScheduledNames: mapScheduledNames(state.entities?.data?.schedule?.data?.data?.schedules)
    };
  },
  {
    request: fetchEntity
  }
)(ScheduleFormComp);

async function handleSubmit(
  monitor,
  setResult,
  request,
  setFilledValues,
  invocation,
  isEditing,
  values
) {
  const {
    platform,
    type,
    sch_name,
    sch_description,
    process,
    t_min,
    t_hr,
    t_day,
    t_mon,
    t_wkd,
    ...params_with_maybe_replvars
  } = values;
  const body = {
    description: sch_description,
    process,
    t_min: t_min || null,
    t_hr: t_hr || null,
    t_day: t_day || null,
    t_mon: t_mon || null,
    t_wkd: t_wkd || null
  };

  if (isEditing) {
    body.rename = sch_name;
  } else {
    body.name = sch_name;
  }

  const { parameters } = extractReplVars(params_with_maybe_replvars);

  function rejector(value, key) {
    if (key === 'parameters') {
      return false;
    }

    if (!value && typeof value !== 'boolean') {
      return false;
    }

    if (value === 'None') {
      return false;
    }

    if (typeof value === 'object' && isEmpty(value)) {
      return false;
    }

    return true;
  }

  if (monitor.enabled) {
    /**
     * Is monitoring a process
     */
    body.process = 'mas.sys.invocation.notify.create';
    body.parameters = {
      name: process,
      parameters: pickBy(parameters, rejector),
      events: monitor.events,
      users: monitor.users
    };
  } else {
    body.parameters = pickBy(parameters, rejector);
  }

  if (isEditing) {
    patchSchedule(invocation.name, body)
      .then(() => {
        setResult('success');
        setFilledValues({});
        request('schedule');
      })
      .catch(error => {
        console.log('[new schedule] new schedule error: ', error); // eslint-disable-line
        setFilledValues(body);
        setResult('fail');
      });
  } else {
    postSchedule(body)
      .then(() => {
        setResult('success');
        setFilledValues({});
        request('schedule');
      })
      .catch(error => {
        console.log('[new schedule] new schedule error: ', error); // eslint-disable-line
        setFilledValues(body);
        setResult('fail');
      });
  }
}

function extractReplVars(rawValues) {
  // find the name + namespace and combine those 2 into a single value
  const values = extractFQName(rawValues);
  const keys = Object.keys(values);

  const result = keys.reduce(
    (extracted, key) => {
      if (key.startsWith('replvar--')) {
        const [index, field_name] = key.replace('replvar--', '').split('--');

        const entry = extracted.replvars[index];

        // ignore deflt propery if is an empty string
        if (field_name === 'deflt' && values[key] === '') {
          return extracted;
        }

        if (entry) {
          if (field_name === 'deflt') {
            entry[field_name] = `str('"'${values[key]}'"')`;
          } else {
            entry[field_name] = values[key];
          }
        } else if (field_name === 'deflt') {
          extracted.replvars.push({ [field_name]: `str('"'${values[key]}'"')` });
        } else {
          extracted.replvars.push({ [field_name]: values[key] });
        }

        return extracted;
      }

      if (key.startsWith('replvalue')) {
        const [index] = key.replace('replvalue--', '').split('--');

        extracted.replvalues[values[`replvar--${index}--name`]] = values[key];

        return extracted;
      }

      extracted.parameters[key] = values[key];

      return extracted;
    },
    { replvars: [], replvalues: {}, parameters: {} }
  );

  return {
    parameters: {
      ...result.parameters,
      replvars: cleanUpReplVars(result.replvars),
      replvalues: result.replvalues
    }
  };
}

function cleanUpReplVars(replvars) {
  return replvars.filter(p => p).map((p, index) => ({ ...p, meta: { position: index + 3 } }));
}

function extractFQName(values) {
  if (values['fqname--name']) {
    if (values['fqname--namespace']) {
      values.name = `${values['fqname--namespace']}.${values['fqname--name']}`;
    } else {
      values.name = values['fqname--name'];
    }
    delete values['fqname--namespace'];
    delete values['fqname--name'];
  }
  return values;
}
