import React, { useRef, useState } from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { Form, Formik } from 'formik';
import { fetchEntity } from '../actions';
import { Switch } from 'shared/components';
import { Button } from 'shared/components/controls';
import { ErrorModal, Modal, ModalHeader } from 'shared/components/overlays';
import { Result } from 'shared/components/overlays/Result/Result';
import { SubmitButton } from 'shared/components/form/SubmitButton';
import { Column, Paper, Row, Section } from 'shared/components/layout';
import { NavigationTab } from 'shared/components/layout/NavigationTab/NavigationTab';
import { Text } from 'shared/components/form/editors/Generic';
import { ProcessParameters } from 'job-process/job-parameters';
import { Notifications } from 'job-process/containers/notifications/notifications';
import { postSchedule } from 'shared/api/schedule';
import { getProcess } from 'shared/api/process';
import { isEmpty, pickBy } from 'lodash';
import SvgIcon from '@material-ui/core/SvgIcon';
import SlowMotionVideoIcon from '@material-ui/icons/SlowMotionVideo';
import { EditProcessModal } from './process-actions/EditProcessModal';
import { hasWritePrivileges } from '../utils/privileges';
import { mapScheduledNames } from '../mappers';
import { CronValidator } from '../../shared/lib/cron-validator/cron-validator';
import { RestClient } from '../../shared/lib/rest';
import moment from 'moment';
import { CONSTANTS } from 'config';

import './ActionsSchedule.scss';

const LinkBehavior = React.forwardRef((props, ref) => <Link ref={ref} {...props} />);

const ActionsProcess = ({ entity }) => {
  const isDisabled = entity?.meta?.appearance === 'Greyed';
  const [open, setOpen] = useState(false);
  const modalActions = {
    open: () => {
      setOpen(true);
    },
    close: () => {
      setOpen(false);
    }
  };
  const message = 'You need to compile this process in order to create a new job.';

  return (
    <>
      {entity?.is_executable === false && (
        <Button
          classes={{ root: 'entity-button' }}
          onClick={() => setOpen(true)}
          disabled={isDisabled}
        >
          New job
        </Button>
      )}
      {entity?.is_executable && (
        <Button
          component={LinkBehavior}
          classes={{ root: 'entity-button' }}
          to={`/jobs/job?process=${entity.name}&redirected=true`}
          disabled={isDisabled}
        >
          New job
        </Button>
      )}
      <ConnectedNewSchedule process={entity} isDisabled={isDisabled} />
      {hasWritePrivileges(entity) && <EditProcessModal process={entity} />}
      <ErrorModal open={open} actions={modalActions} message={message} />
    </>
  );
};

export default ActionsProcess;

export const ConnectedNewSchedule = connect(
  state => {
    return {
      allScheduledNames: mapScheduledNames(state.entities?.data?.schedule?.data?.data?.schedules)
    };
  },
  {
    request: fetchEntity
  }
)(NewSchedule);

function NewSchedule({ process, isDisabled, request, allScheduledNames }) {
  const [open, setOpen] = useState(false);
  const modalActions = {
    open: () => {
      setOpen(true);
    },
    close: () => {
      setOpen(false);
      setResult('');
      setFilledValues({});
    }
  };
  const [openErrorModal, setOpenErrorModal] = useState(false);
  const errorModalActions = {
    open: () => {
      setOpenErrorModal(true);
    },
    close: () => {
      setOpenErrorModal(false);
    }
  };
  const message = 'You need to compile this process in order to create a new schedule.';

  const formRef = useRef();
  const [result, setResult] = useState('');
  // processDetails might be used at a later date
  const [processDetails, setProcessDetails] = useState({}); // eslint-disable-line
  const [tab, setTab] = useState('schedule');
  const [filledValues, setFilledValues] = useState({});

  const onClickHandler = () => {
    if (process.is_executable) {
      setOpen(true);
    } else {
      setOpenErrorModal(true);
    }

    getProcess(process.name)
      .then(data => {
        if (data.data.data && data.data.data.processes && data.data.data.processes[0]) {
          setProcessDetails(data.data.data.processes[0]);
        }
      })
      .catch(error => {
        console.log('[get process] error getting process details: ', error); // eslint-disable-line
      });
  };

  return (
    <>
      <Button
        classes={{ root: 'entity-button' }}
        onClick={() => {
          onClickHandler();
        }}
        disabled={isDisabled}
      >
        New Schedule
      </Button>
      <Modal open={open} actions={modalActions}>
        <Paper
          style={{
            width: '75vw',
            height: '75vh'
          }}
        >
          <ModalHeader
            title="New Schedule"
            icon={
              <SvgIcon>
                <SlowMotionVideoIcon />
              </SvgIcon>
            }
            formikRef={formRef}
          />
          <Switch value={result}>
            <Switch.Case value="success">
              <Result type="success">
                <Result.Text>Schedule was added successfully!</Result.Text>
              </Result>
              <Paper.Footer>
                <Paper.Footer.Right>
                  <Button
                    onClick={() => {
                      modalActions.close();
                    }}
                  >
                    Done
                  </Button>
                </Paper.Footer.Right>
              </Paper.Footer>
            </Switch.Case>
            <Switch.Case value="fail">
              <Result type="fail">
                <Result.Text>
                  <p>An error occurred and the new schedule could not be added.</p>
                  <p style={{ color: '#3765af', textDecoration: 'underline', cursor: 'pointer' }}>
                    <a
                      role="presentation"
                      onClick={() => {
                        setResult('');
                      }}
                    >
                      Go back to form
                    </a>
                  </p>
                </Result.Text>
              </Result>
              <Paper.Footer>
                <Paper.Footer.Right>
                  <Button
                    onClick={() => {
                      modalActions.close();
                    }}
                  >
                    Close
                  </Button>
                </Paper.Footer.Right>
              </Paper.Footer>
            </Switch.Case>
            <Switch.Case value="">
              <Paper.Header>
                <ViewTabs active={tab} changeTab={setTab} />
              </Paper.Header>
              <Switch value={tab}>
                <Switch.Case value="schedule">
                  <ScheduleForm
                    process={process}
                    setResult={setResult}
                    allScheduledNames={allScheduledNames}
                    request={request}
                    filledValues={filledValues}
                    setFilledValues={setFilledValues}
                    formRef={formRef}
                  />
                </Switch.Case>
                <Switch.Case value="documentation">
                  <Paper.Content>
                    <Documentation process={process} />
                  </Paper.Content>
                </Switch.Case>
              </Switch>
            </Switch.Case>
          </Switch>
        </Paper>
      </Modal>
      <ErrorModal open={openErrorModal} actions={errorModalActions} message={message} />
    </>
  );
}

function ViewTabs({ active, changeTab }) {
  const tabs = [
    { label: 'Schedule', value: 'schedule', active: true },
    { label: 'Documentation', value: 'documentation' }
  ];

  return <NavigationTab active={active} onClick={changeTab} tabs={tabs} />;
}

class ScheduleForm extends React.PureComponent {
  derivedMonitor = {
    enabled: false,
    events: 0,
    users: []
  };

  constructor(props) {
    super(props);

    this.state = {
      monitor: this.derivedMonitor,
      serverTime: null
    };
  }

  componentDidMount() {
    RestClient.get(CONSTANTS.BASE.ENDPOINTS.CURRENT_TIME).then((response) => {
      this.setState({ serverTime: response.data.data });
    });
  }

  render() {
    const {
      process,
      setResult,
      allScheduledNames,
      request,
      filledValues,
      setFilledValues,
      formRef
    } = this.props;

    const { monitor, serverTime, everyHourWarningShown } = this.state;
    const masTimeFormatted = serverTime ? moment(serverTime.currentTime).format('HH:mm MMM DD YYYY') : null;

    const extraValidations = values => {
      const { t_min, t_hr, t_day, t_mon, t_wkd, sch_name } = values;
      const errors = {};

      if (sch_name) {
        if (sch_name.includes('.')) {
          errors.sch_name = 'Name cannot include "."';
        } else if (sch_name.includes(' ')) {
          errors.sch_name = 'Name cannot include spaces';
        } else if (allScheduledNames.includes(sch_name)) {
          errors.sch_name = 'A schedule with this name already exists';
        }
      }

      if (
        values['fqname--name'] &&
        (!/^(?:[a-z_][a-z0-9_.]*)$/i.test(values['fqname--name']) ||
          values['fqname--name'].substr(-1) === '.')
      ) {
        errors['fqname--name'] = 'A valid MAS relative name is required.';
      }

      this.setState({ everyHourWarningShown: t_hr === '*' });

      if (t_hr === '*' && t_day === '*' && t_mon === '*' && t_wkd === '*') {
        errors.t_hr = 'At least one value should be populated';
        errors.t_day = 'At least one value should be populated';
        errors.t_mon = 'At least one value should be populated';
        errors.t_wkd = 'At least one value should be populated';
        return errors;
      }

      if (!CronValidator.isValidMinutes(t_min)) {
        errors.t_min = CronValidator.FORMAT_MSG.MINUTES;
      }

      if (!CronValidator.isValidHours(t_hr)) {
        errors.t_hr = CronValidator.FORMAT_MSG.HOURS;
      }

      if (!CronValidator.isValidDays(t_day)) {
        errors.t_day = CronValidator.FORMAT_MSG.DAYS;
      }

      if (!CronValidator.isValidMonth(t_mon)) {
        errors.t_mon = CronValidator.FORMAT_MSG.MONTH;
      }

      if (!CronValidator.isValidWeekDay(t_wkd)) {
        errors.t_wkd = CronValidator.FORMAT_MSG.DAY_OF_WEEK;
      }

      return errors;
    };

    return (
      <>
        <Formik
          initialValues={{ process: process.name }}
          validate={extraValidations}
          onSubmit={values => {
            handleSubmit(monitor, setResult, request, setFilledValues, values);
          }}
          innerRef={formRef}
        >
          {() => {
            return (
              <>
                <Paper.Content>
                  <Form>
                    <Section>
                      <Section.Header>
                        <Section.Title>Process</Section.Title>
                      </Section.Header>
                      <Section.Content>
                        <Row>
                          <Text
                            name="process"
                            label="Process"
                            description={process.description}
                            value={process.name}
                            disabled
                          />
                        </Row>
                      </Section.Content>
                    </Section>
                    <Section>
                      <Section.Header>
                        <Section.Title>Schedule</Section.Title>
                      </Section.Header>
                      <Section.Content>
                        <Row>
                          <Text
                            name="sch_name"
                            label="Name"
                            value={filledValues?.name || undefined}
                            required
                          />
                        </Row>
                        <Row>
                          <Text
                            name="sch_description"
                            label="Description"
                            value={filledValues?.description || undefined}
                            multiline
                            required
                          />
                        </Row>

                        <Section.Title>Server time: {masTimeFormatted}</Section.Title>

                        <Row>
                          <Column style={{ maxWidth: '20%', width: '20%' }}>
                            <Text
                              name="t_min"
                              label="T_min"
                              value={filledValues?.t_min || CronValidator.ALLOWED_MINUTES_OPTIONS[0]}
                              description={CronValidator.FORMAT_MSG.MINUTES}
                              options={CronValidator.ALLOWED_MINUTES_OPTIONS.map(option => ({
                                value: option,
                                label: option
                              }))}
                            />
                          </Column>
                          <Column style={{ maxWidth: '20%', width: '20%' }}>
                            <Text name="t_hr" label="T_hr" value={filledValues?.t_hr || '*'} description={CronValidator.FORMAT_MSG.HOURS} />
                            {everyHourWarningShown && (
                              <div className="every-hour-warning">{CronValidator.FORMAT_MSG.EVERY_HOUR_WARNING}</div>
                            )}
                          </Column>
                          <Column style={{ maxWidth: '20%', width: '20%' }}>
                            <Text name="t_day" label="T_Day" value={filledValues?.t_day || '*'} description={CronValidator.FORMAT_MSG.DAYS} />
                          </Column>
                          <Column style={{ maxWidth: '20%', width: '20%' }}>
                            <Text name="t_mon" label="T_mon" value={filledValues?.t_mon || '*'} description={CronValidator.FORMAT_MSG.MONTH} />
                          </Column>
                          <Column style={{ maxWidth: '20%', width: '20%' }}>
                            <Text name="t_wkd" label="T_wkd" value={filledValues?.t_wkd || '*'} description={CronValidator.FORMAT_MSG.DAY_OF_WEEK} />
                          </Column>
                        </Row>
                      </Section.Content>
                    </Section>
                    <ProcessParameters
                      recieved={filledValues?.parameters || process.parameters}
                      editable
                    />
                    <Section>
                      <Section.Header>
                        <Section.Title>Notifications</Section.Title>
                      </Section.Header>
                      <Section.Content>
                        <Notifications
                          name="notifications"
                          recievedData={this.derivedMonitor}
                          onApply={n_state => {
                            if (n_state.events === 0 || isEmpty(n_state.users)) {
                              this.setState({ monitor: { enabled: false } });
                            }
                            this.setState({ monitor: { enabled: true, ...n_state } });
                          }}
                        />
                      </Section.Content>
                    </Section>
                  </Form>
                </Paper.Content>
                <Paper.Footer>
                  <Paper.Footer.Right>
                    <SubmitButton variant="contained">Save</SubmitButton>
                  </Paper.Footer.Right>
                </Paper.Footer>
              </>
            );
          }}
        </Formik>
      </>
    );
  }
}

async function handleSubmit(monitor, setResult, request, setFilledValues, values) {
  const {
    sch_name,
    sch_description,
    process,
    t_min,
    t_hr,
    t_day,
    t_mon,
    t_wkd,
    ...params_with_maybe_replvars
  } = values;
  const body = {
    name: sch_name,
    description: sch_description,
    process,
    t_min: t_min || null,
    t_hr: t_hr || null,
    t_day: t_day || null,
    t_mon: t_mon || null,
    t_wkd: t_wkd || null
  };

  const { parameters } = extractReplVars(params_with_maybe_replvars);

  function rejector(value, key) {
    if (key === 'parameters') {
      return false;
    }

    if (!value && typeof value !== 'boolean') {
      return false;
    }

    if (value === 'None') {
      return false;
    }

    if (typeof value === 'object' && isEmpty(value)) {
      return false;
    }

    return true;
  }

  if (monitor.enabled) {
    /**
     * Is monitoring a process
     */
    body.process = 'mas.sys.invocation.notify.create';
    body.parameters = {
      name: process,
      parameters: pickBy(parameters, rejector),
      events: monitor.events,
      users: monitor.users
    };
  } else {
    body.parameters = pickBy(parameters, rejector);
  }

  postSchedule(body)
    .then(() => {
      setResult('success');
      setFilledValues({});
      request('schedule');
    })
    .catch(error => {
      console.log('[new schedule] new schedule error: ', error); // eslint-disable-line
      setFilledValues(body);
      setResult('fail');
    });
}

function extractReplVars(rawValues) {
  // find the name + namespace and combine those 2 into a single value
  const values = extractFQName(rawValues);
  const keys = Object.keys(values);

  const result = keys.reduce(
    (extracted, key) => {
      if (key.startsWith('replvar--')) {
        const [index, field_name] = key.replace('replvar--', '').split('--');

        const entry = extracted.replvars[index];

        // ignore deflt propery if is an empty string
        if (field_name === 'deflt' && values[key] === '') {
          return extracted;
        }

        if (entry) {
          if (field_name === 'deflt') {
            entry[field_name] = `str('"'${values[key]}'"')`;
          } else {
            entry[field_name] = values[key];
          }
        } else if (field_name === 'deflt') {
          extracted.replvars.push({ [field_name]: `str('"'${values[key]}'"')` });
        } else {
          extracted.replvars.push({ [field_name]: values[key] });
        }

        return extracted;
      }

      if (key.startsWith('replvalue')) {
        const [index] = key.replace('replvalue--', '').split('--');

        extracted.replvalues[values[`replvar--${index}--name`]] = values[key];

        return extracted;
      }

      extracted.parameters[key] = values[key];

      return extracted;
    },
    { replvars: [], replvalues: {}, parameters: {} }
  );

  return {
    parameters: {
      ...result.parameters,
      replvars: cleanUpReplVars(result.replvars),
      replvalues: result.replvalues
    }
  };
}

function cleanUpReplVars(replvars) {
  return replvars.filter(p => p).map((p, index) => ({ ...p, meta: { position: index + 3 } }));
}

function extractFQName(values) {
  if (values['fqname--name']) {
    if (values['fqname--namespace']) {
      values.name = `${values['fqname--namespace']}.${values['fqname--name']}`;
    } else {
      values.name = values['fqname--name'];
    }
    delete values['fqname--namespace'];
    delete values['fqname--name'];
  }
  return values;
}

const Documentation = ({ process }) => {
  return (
    <Section>
      <Section.Header>
        <Section.Title>{process.name}</Section.Title>
        <Section.Info>{process.description}</Section.Info>
      </Section.Header>
      {process.parameters && process.parameters.length > 0 && (
        <Section.Content>
          <h2>
            <b>Parameters:</b>
          </h2>
          <div>
            {process.parameters.map(parameter => {
              return (
                <React.Fragment key={parameter.name}>
                  <h3>
                    {parameter.name} ({parameter.data_type})
                  </h3>
                  <Section.Info>{parameter.description}</Section.Info>
                </React.Fragment>
              );
            })}
          </div>
        </Section.Content>
      )}
    </Section>
  );
};
