import { mapAccountsTree } from '../../../../explorer/store/mappers';

export const ADDITIONAL_ACCOUNT_FILTERS = {
  dbms: [
    filterDbmsByShell.bind(null, 'shell'),
    filterDbmsByShell.bind(null, 'target_shell'),
  ],
  target_shell: [filterShellByPlatform],
  shell: [filterShellByPlatform],
};

function filterDbmsByShell(shellFieldName, accounts, formValues) {
  accounts = accounts || [];
  if (!formValues || !formValues[shellFieldName]) {
    return accounts;
  }

  const shellAccount = accounts.find((account) => account.name === formValues[shellFieldName]);
  if (!shellAccount) {
    return accounts;
  }

  return accounts.filter(account => account.address === shellAccount.address);
}

const PLATFORM_VALUE_TO_TYPE = {
  Oracle: 'ORACLE',
  PostgreSQL: 'PGSQL',
  MySQL: 'MYSQL',
  'Microsoft SQL Server': 'MSSQL',
};

function filterShellByPlatform(accounts, formValues) {
  accounts = accounts || [];

  if (!formValues || !formValues.platform) {
    return accounts;
  }

  const accountsTree = mapAccountsTree(accounts);
  const platformTree = accountsTree.find((platform) => platform.name === PLATFORM_VALUE_TO_TYPE[formValues.platform]);
  if (!platformTree) {
    return accounts;
  }

  const platformAddresses = Object.keys(platformTree.servers);
  return accounts.filter(account => platformAddresses.indexOf(account.address) !== -1);
}
