import { RestClient } from 'shared/lib/rest';

export async function postInvocation(body) {
  try {
    const { data } = await RestClient.post('/invocation', body);

    return { result: data.result, invocations: data.data.invocations };
  } catch (e) {
    return { result: 'fail', invocations: [], messages: e.messages };
  }
}

export function requestInvocation(uuid) {
  return RestClient.get(`/invocation/${uuid}`);
}

export async function fetchInvocation(uuid) {
  try {
    const response = await RestClient.get(`/invocation/${uuid}`);

    return {
      result: 'success',
      invocation: response.data.data.invocations[0]
    };
  } catch (e) {
    console.log(e); // eslint-disable-line
    return { result: 'fail' };
  }
}

export async function fetchInvocationOutput(uuid) {
  return RestClient.get(`/invocation/${uuid}/display`, {
    page_size: 2147483645
  });
}

export async function invocationOutputPoller(uuid) {
  let statusChecksCount = 0;
  let pollerFinished = false;
  let succeeded = false;

  while (statusChecksCount < 10 && !pollerFinished) {
    const invocationStatus = await sleep(fetchInvocation.bind(this, uuid)); // eslint-disable-line
    statusChecksCount++;
    succeeded = invocationStatus.invocation.status === 'SUCCEEDED';
    pollerFinished = succeeded || invocationStatus.invocation.status === 'FAILED';
  }

  return succeeded ? fetchInvocationOutput(uuid) : Promise.resolve(null);
}

export function parseInvocationResult(invocationOutput) {
  const resultText = invocationOutput && invocationOutput.data.data.outputs ? invocationOutput.data.data.outputs[0].data.text : null;
  const resultJsonString = (resultText || '').split('\n')[1];
  return resultJsonString ? JSON.parse(resultJsonString) : [];
}

function timeout(ms) {
  return new Promise(resolve => setTimeout(resolve, ms));
}

async function sleep(fn, ...args) {
  await timeout(1000);
  return fn(...args);
}
