import React from 'react';
import { node, bool } from 'prop-types';
import classnames from 'classnames';
import './Main.local.scss';

export const Main = ({ flex, mdPage, children }) => (
  <main
    className={classnames('page-main', { flex, 'md-page': mdPage })}
  >
    {children}
  </main>
);

Main.propTypes = {
  children: node,
  flex: bool,
  mdPage: bool,
};

Main.defaultProps = {
  flex: true,
  mdPage: false
};
