import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import {
  changeStateThunk,
  disableSessionsLoadingFlag,
  enableSessionsLoadingFlag,
  sessionsLoaderResolver
} from '../../store/thunks';
import { Paper } from 'shared/components/layout';
import { NavigationTab } from '../../components/NavigationTab/NavigationTab';
import { Button } from 'shared/components/controls';
import { FormattedMessage } from 'react-intl';
import { getFullPath, getSortedSessions } from '../../store/selectors';
import { CommonSessionsTable } from './tables/CommonSessionsTable';
import { DbmsType } from '../../SystemExplorerFeatureUtil';
import { MssqlSessionsTable } from './tables/MssqlSessionsTable';
import './Sessions.local.scss';

export class Sessions extends React.PureComponent {
  componentDidMount() {
    this.getSessionsData();
  }

  render() {
    const {
      level,
      sessions,
      isLoading,
      changeTab,
      fullPath,
      enableLoadingFlag,
      disableLoadingFlag,
      storeState: { path }
    } = this.props;

    const [protocol] = path.split('--');

    return (
      <>
        <Paper.Header>
          <NavigationTab active={level} onClick={changeTab} protocol={protocol} />
        </Paper.Header>
        <Paper.Content>
          {protocol !== DbmsType.MSSQL && (
            <CommonSessionsTable isLoading={isLoading} sessions={sessions} />
          )}
          {protocol === DbmsType.MSSQL && (
            <MssqlSessionsTable
              isLoading={isLoading}
              sessions={sessions}
              dbms={fullPath}
              refreshSessions={this.getSessionsData}
              enableLoadingFlag={enableLoadingFlag}
              disableLoadingFlag={disableLoadingFlag}
            />
          )}
        </Paper.Content>
        <Paper.Footer>
          <Button className="qa-refresh-btn align-right" onClick={this.getSessionsData}>
            <FormattedMessage id="general.refresh" />
          </Button>
        </Paper.Footer>
      </>
    );
  }

  getSessionsData = () => {
    const {
      fullPath,
      storeState: { path }
    } = this.props;
    const [protocol] = path.split('--');

    this.props.getSessions({
      fullPath,
      protocol,
      page_size: 2147483645
    });
  };
}

Sessions.propTypes = {
  level: PropTypes.string.isRequired,
  isLoading: PropTypes.bool,
  fullPath: PropTypes.string.isRequired,
  changeTab: PropTypes.func
};

export const ConnectedSessions = connect(
  state => ({
    sessions: getSortedSessions(state),
    level: state.explorer.state.level,
    isLoading: state.explorer.flags.sessions.loading,
    fullPath: getFullPath(state),
    storeState: state.explorer.state
  }),
  dispatch => ({
    changeTab: newState => dispatch(changeStateThunk({ level: newState })),
    getSessions: filters => dispatch(sessionsLoaderResolver(filters)),
    enableLoadingFlag: () => dispatch(enableSessionsLoadingFlag()),
    disableLoadingFlag: () => dispatch(disableSessionsLoadingFlag())
  })
)(Sessions);
