import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { changeStateThunk, objectsLoaderResolver, setSelectedObjectThunk } from '../../store/thunks';
import { Paper } from 'shared/components/layout';
import { NavigationTab } from '../../components/NavigationTab/NavigationTab';
import { DbObjects } from '../../components/DbObjects';
import {
  getFullPath,
  getInvalidObjects,
  getStructuredObjects,
  haveObjectsData,
  objectsSelectorResolver
} from '../../store/selectors';

const Objects = ({
  storeState: { level, path },
  changeTab,
  fullPath,
  getObjects,
  objectsData,
  invalidObjects,
  objectDetails,
  selectedItem,
  setObjectsItem,
  loadingFlags,
  storedEntries
}) => {
  const dbName = path.split('--')[2];
  const [protocol] = path.split('--');
  useEffect(() => {
    if (!storedEntries) {
      getObjects({
        fullPath,
        protocol
      });
    }
  }, [fullPath]);

  return (
    <>
      <Paper.Header>
        <NavigationTab active={level} onClick={changeTab} protocol={protocol} />
      </Paper.Header>
      <Paper.Content>
        <DbObjects
          loadingFlags={loadingFlags}
          type={level}
          invalidObjects={invalidObjects}
          data={objectDetails}
          list={objectsData}
          selectedItem={selectedItem}
          setItem={setObjectsItem}
          dbName={dbName}
          protocol={protocol}
        />
      </Paper.Content>
    </>
  );
};

Objects.propTypes = {
  storeState: PropTypes.object,
  objectsData: PropTypes.array,
  objectDetails: PropTypes.object,
  loadingFlags: PropTypes.object,
  fullPath: PropTypes.string,
  changeTab: PropTypes.func,
  setObjectsItem: PropTypes.func,
  selectedItem: PropTypes.object,
  getObjects: PropTypes.func
};

export const ConnectedObjects = connect(
  state => {
    const { state: { path } } = state.explorer;
    const [protocol] = path.split('--');

    return {
      storeState: state.explorer.state,
      objectsData: getStructuredObjects(state),
      selectedItem: {
        owner: state.explorer.objects.selectedOwner,
        object: state.explorer.objects.selectedObject
      },
      loadingFlags: state.explorer.flags.objects,
      objectDetails: objectsSelectorResolver(state, protocol),
      invalidObjects: getInvalidObjects(state),
      fullPath: getFullPath(state),
      storedEntries: haveObjectsData(state)
    };
  },
  dispatch => ({
    changeTab: level => dispatch(changeStateThunk({ level })),
    setObjectsItem: object => dispatch(setSelectedObjectThunk(object)),
    getObjects: filters => dispatch(objectsLoaderResolver(filters))
  })
)(Objects);
