export const DbmsType = {
  ORACLE: 'ORACLE',
  POSTGRES: 'PGSQL',
  MYSQL: 'MYSQL',
  MSSQL: 'MSSQL',
  OTHER: 'OTHER',
};

export const DbmsFeature = {
  WORKLOAD: 'WORKLOAD',
  SESSIONS: 'SESSIONS',
  TOP_SQL: 'TOP_SQL',
  OBJECTS: 'OBJECTS',
  STORAGE: 'STORAGE',
  LICENSE_REPORTING: 'LICENSE_REPORTING',
};

const FEATURE_BY_TYPE_MAP = {
  [DbmsType.ORACLE]: [DbmsFeature.WORKLOAD, DbmsFeature.OBJECTS, DbmsFeature.STORAGE, DbmsFeature.SESSIONS, DbmsFeature.TOP_SQL, DbmsFeature.LICENSE_REPORTING],
  [DbmsType.MYSQL]: [DbmsFeature.WORKLOAD, DbmsFeature.TOP_SQL],
  [DbmsType.POSTGRES]: [DbmsFeature.WORKLOAD, DbmsFeature.TOP_SQL],
  [DbmsType.MSSQL]: [DbmsFeature.WORKLOAD, DbmsFeature.OBJECTS, DbmsFeature.SESSIONS, DbmsFeature.TOP_SQL],
  [DbmsType.OTHER]: [DbmsFeature.WORKLOAD],
};

const LOAD_SESSIONS_BY_PROCESS = [DbmsType.MSSQL];
const LOAD_OBJECTS_BY_PROCESS = [DbmsType.MSSQL];
const OBJECTS_SIDEBAR_DISABLED = [DbmsType.MSSQL];

const SESSIONS_ENDPOINT_MAP = {
  [DbmsType.MSSQL]: 'vendita.microsoft.sqlserver.monitor.sessions.sp_who'
};

const OBJECTS_ENDPOINT_MAP = {
  [DbmsType.MSSQL]: 'vendita.microsoft.sqlserver.monitor.database.all_objects'
};

const ACCOUNT_ENTITY_PREFIX = 'mas0000.';
const ACCOUNT_ENTITY_NAME_TOKEN = '.account.';

const DBMS_LIST_ORDER = {
  [DbmsType.ORACLE]: 50,
  [DbmsType.MSSQL]: 40,
  [DbmsType.MYSQL]: 30,
  [DbmsType.POSTGRES]: 20,
  [DbmsType.OTHER]: 10,
};

const OBJECTS_DATA_MAPPERS = {
  [DbmsType.MSSQL]: (item) => {
    return {
      OBJECT_NAME: item.name,
      CREATED: item.create_date,
      OBJECT_ID: item.object_id,
      LAST_DDL_TIME: item.modify_date,
    };
  },
};

export class SystemExplorerFeatureUtil {
  static isFeatureAllowed(dbmsType, feature) {
    if (!dbmsType || !feature || !FEATURE_BY_TYPE_MAP[dbmsType]) {
      return false;
    }
    return FEATURE_BY_TYPE_MAP[dbmsType].includes(feature);
  }

  static isWorkloadAllowed(dbmsType) {
    return SystemExplorerFeatureUtil.isFeatureAllowed(dbmsType, DbmsFeature.WORKLOAD);
  }

  static isSessionsAllowed(dbmsType) {
    return SystemExplorerFeatureUtil.isFeatureAllowed(dbmsType, DbmsFeature.SESSIONS);
  }

  static isTopSqlAllowed(dbmsType) {
    return SystemExplorerFeatureUtil.isFeatureAllowed(dbmsType, DbmsFeature.TOP_SQL);
  }

  static isObjectsAllowed(dbmsType) {
    return SystemExplorerFeatureUtil.isFeatureAllowed(dbmsType, DbmsFeature.OBJECTS);
  }

  static isObjectsSidebarDisabled(dbmsType) {
    return OBJECTS_SIDEBAR_DISABLED.includes(dbmsType);
  }

  static isStorageAllowed(dbmsType) {
    return SystemExplorerFeatureUtil.isFeatureAllowed(dbmsType, DbmsFeature.STORAGE);
  }

  static isLicenseReportingAllowed(dbmsType) {
    return SystemExplorerFeatureUtil.isFeatureAllowed(dbmsType, DbmsFeature.LICENSE_REPORTING);
  }

  static splitAccountName(fullName) {
    fullName = fullName || '';
    if (!fullName.startsWith(ACCOUNT_ENTITY_PREFIX)) {
      return '';
    }
    const startIdx = fullName.lastIndexOf(ACCOUNT_ENTITY_NAME_TOKEN);
    if (startIdx === -1) {
      return '';
    }
    return fullName.substr(startIdx + ACCOUNT_ENTITY_NAME_TOKEN.length);
  }

  static shouldLoadSessionsByProcess(dbmsType) {
    return LOAD_SESSIONS_BY_PROCESS.includes(dbmsType);
  }

  static shouldLoadObjectsByProcess(dbmsType) {
    return LOAD_OBJECTS_BY_PROCESS.includes(dbmsType);
  }

  static getSessionsEndpoint(dbmsType) {
    return SESSIONS_ENDPOINT_MAP[dbmsType];
  }

  static getObjectsEndpoint(dbmsType) {
    return OBJECTS_ENDPOINT_MAP[dbmsType];
  }

  static getObjectsDataMapper(dbmsType) {
    return OBJECTS_DATA_MAPPERS[dbmsType];
  }

  static getListOrder(dbmsType) {
    return DBMS_LIST_ORDER[dbmsType] || 0;
  }
}
