import React from 'react';
import { connect } from 'react-redux';
import { ServersTile } from '../../components/ServersTile/ServersTile';
import { DatabasesTile } from '../../components/DatabasesTile/DatabasesTile';
import { OptionsTile } from '../../components/OptionsTile/OptionsTile';

import './Dashboard.local.scss';

function Dashboard({ selected }) {
  return (
    <>
      <div className="widgets-row">
        <div className="widget">
          <ServersTile servers={selected} />
        </div>
        <div className="widget">
          <DatabasesTile servers={selected} />
        </div>
      </div>
      <OptionsTile servers={selected} />
    </>
  );
}

export const ConnectedDashboard = connect(state => {
  return {
    selected: state.executiveDashboard.selectedServers
  };
})(Dashboard);
