import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { changeStateThunk, loadTopSQLStatementsThunk } from '../../store/thunks';
import { getFullPath, selectTopSqlStatements } from '../../store/selectors';
import { Paper } from 'shared/components/layout';
import { NavigationTab } from '../../components/NavigationTab/NavigationTab';
import { FormattedMessage } from 'react-intl';
import { Button } from '../../../shared/components/controls';
import { ElapsedTimeChart } from './charts/ElapsedTimeChart';
import { TopSqlStatementsUtil } from './TopSqlStatementsUtil';
import { OracleTopSQLTable } from './tables/OracleTopSQLTable';
import { PostgresTopSQLTable } from './tables/PostgresTopSQLTable';
import './TopSQLStatements.local.scss';
import { MysqlTopSQLTable } from './tables/MysqlTopSQLTable';
import { DbmsType } from '../../SystemExplorerFeatureUtil';
import { MssqlTopSQLTable } from './tables/MssqlTopSQLTable';

export class TopSQLStatements extends React.PureComponent {
  componentDidMount() {
    this.getTopSqlStatementsData();
  }

  render() {
    const {
      storeState: { level, path },
      topSqlStatements,
      isLoading,
      changeTab
    } = this.props;

    const [protocol] = path.split('--');
    const elapsedTimeChartData = TopSqlStatementsUtil.isChartSupported(protocol) ? this.prepareChartData(topSqlStatements) : null;

    return (
      <>
        <Paper.Header>
          <NavigationTab active={level} onClick={changeTab} protocol={protocol} />
        </Paper.Header>
        <Paper.Content>
          {protocol === DbmsType.ORACLE && (
            <OracleTopSQLTable topSqlStatements={topSqlStatements} isLoading={isLoading} />
          )}
          {protocol === DbmsType.POSTGRES && (
            <PostgresTopSQLTable topSqlStatements={topSqlStatements} isLoading={isLoading} />
          )}
          {protocol === DbmsType.MYSQL && (
            <MysqlTopSQLTable topSqlStatements={topSqlStatements} isLoading={isLoading} />
          )}
          {protocol === DbmsType.MSSQL && (
            <MssqlTopSQLTable topSqlStatements={topSqlStatements} isLoading={isLoading} />
          )}
          {TopSqlStatementsUtil.isChartSupported(protocol) && (
            <div className="graph-container">
              {elapsedTimeChartData.length > 0 && (
                <ElapsedTimeChart data={elapsedTimeChartData} />
              )}
            </div>
          )}
        </Paper.Content>
        <Paper.Footer>
          <Button className="qa-refresh-btn align-right" onClick={this.getTopSqlStatementsData}>
            <FormattedMessage id="general.refresh" />
          </Button>
        </Paper.Footer>
      </>
    );
  }

  getTopSqlStatementsData = () => {
    const {
      storeState: { path },
      fullPath
    } = this.props;
    const [protocol] = path.split('--');
    const dbmsProperty = TopSqlStatementsUtil.getDbmsPropertyName(protocol);
    const payload = {
      parameters: {
        oformat: 'JSON',
        [dbmsProperty]: fullPath,
        compact: true
      },
      process: TopSqlStatementsUtil.getEndpoint(protocol),
      timestamp: null
    };

    this.props.getTopSqlStatements(payload);
  };

  prepareChartData = (topStatementsData) => {
    return (topStatementsData || []).map((row) => {
      const timeMillis = Math.round(row.ELAPSED_TIME / 1000);
      const cpuTime = Math.round((timeMillis / 100) * row.PCT_CPU);
      const ioTime = Math.round((timeMillis / 100) * row.PCT_IO);
      return {
        sqlId: row.SQL_ID,
        timeMillisFull: timeMillis,
        timeMillis: timeMillis - cpuTime - ioTime,
        cpuTime,
        ioTime,
        cpuPercent: row.PCT_CPU,
        ioPercent: row.PCT_IO
      };
    });
  }
}

TopSQLStatements.propTypes = {
  isLoading: PropTypes.bool,
  getTopSqlStatements: PropTypes.func,
  storeState: PropTypes.object,
  topSqlStatements: PropTypes.array
};

export const ConnectedTopSQLStatements = connect(
  state => ({
    topSqlStatements: selectTopSqlStatements(state),
    isLoading: state.explorer.flags.topSqlStatements.loading,
    storeState: state.explorer.state,
    fullPath: getFullPath(state)
  }),
  dispatch => ({
    changeTab: newState => dispatch(changeStateThunk({ level: newState })),
    getTopSqlStatements: filters => dispatch(loadTopSQLStatementsThunk(filters))
  })
)(TopSQLStatements);
