import React from 'react';
import { string } from 'prop-types';
import { STATUS_TYPES } from '../../store/selectors';

import './StatusLabel.local.scss';

const STATUS_LABEL_MAP = {
  [STATUS_TYPES.down]: 'Down',
  [STATUS_TYPES.up]: 'Up',
  [STATUS_TYPES.error]: 'Has errors',
  [STATUS_TYPES.notConnected]: 'Not connected',
  [STATUS_TYPES.notFound]: 'Not found',
};

export class StatusLabel extends React.PureComponent {
  render() {
    const {
      status
    } = this.props;

    return (
      <div className={`status ${status.replace(/\s/g, '-')}`} role="presentation">{STATUS_LABEL_MAP[status]}</div>
    );
  }
}

StatusLabel.propTypes = {
  status: string
};
