import React from 'react';
import { Modal, ModalHeader } from 'shared/components/overlays';
import { Paper } from 'shared/components/layout';
import { Result } from 'shared/components/overlays/Result/Result';
import { Button } from 'shared/components/controls';
import CheckCircleRoundedIcon from '@material-ui/icons/CheckCircleRounded';
import SvgIcon from '@material-ui/core/SvgIcon';
import './SuccessModal.local.scss';

export const SuccessModal = ({ open, actions, message }) => {
  return (
    <Modal open={open} actions={actions}>
      <Paper
        style={{
          width: '50vw',
          height: '50vh'
        }}
      >
        <ModalHeader
          title="Success"
          icon={
            <SvgIcon className="header-icon">
              <CheckCircleRoundedIcon />
            </SvgIcon>
          }
        />
        <Paper.Content scrollable={false}>
          <Result type="success">
            <Result.Text>{message}</Result.Text>
          </Result>
        </Paper.Content>
        <Paper.Footer>
          <Paper.Footer.Right>
            <Button
              onClick={() => {
                actions.close();
              }}
            >
              OK
            </Button>
          </Paper.Footer.Right>
        </Paper.Footer>
      </Paper>
    </Modal>
  );
};
