export function loadSessionsByProcessThunk(filters) {
  return async dispatch => {
    dispatch(creators.loading(LOAD_SESSIONS));

    const { fullPath, protocol } = filters;
    const dbmsProperty = TopSqlStatementsUtil.getDbmsPropertyName(protocol);
    const payload = {
      parameters: {
        oformat: 'JSON',
        [dbmsProperty]: fullPath,
        compact: true
      },
      process: SystemExplorerFeatureUtil.getSessionsEndpoint(protocol),
      timestamp: null
    };

    try {
      const creationResult = await postInvocation(payload);
      if (creationResult.invocations.length) {
        const invocationUuid = creationResult.invocations[0].uuid;
        let statusChecksCount = 0;
        let isFinished = false;

        while (statusChecksCount < 10 && !isFinished) {
          const invocationStatus = await sleep(fetchInvocation.bind(this, invocationUuid)); // eslint-disable-line
          statusChecksCount++;
          isFinished = invocationStatus.invocation.status === 'SUCCEEDED' || invocationStatus.invocation.status === 'FAILED';
        }

        const invocationOutput = await fetchInvocationOutput(invocationUuid);
        const resultTxt = invocationOutput.data.data.outputs ? invocationOutput.data.data.outputs[0].data.text : null;
        const statementsJson = (resultTxt || '').split('\n')[1];
        const sessionsData = statementsJson ? JSON.parse(statementsJson) : [];
        dispatch(creators.success(LOAD_SESSIONS, { data: sessionsData, sort: filters.sort }));
      } else {
        dispatch(creators.fail(LOAD_SESSIONS, 'Did not find the invocation'));
      }
    } catch (error) {
      dispatch(creators.fail(LOAD_SESSIONS, error));
    }
  };
}

export function sessionsLoaderResolver(filters) {
  const { protocol } = filters;
  if (SystemExplorerFeatureUtil.shouldLoadSessionsByProcess(protocol)) {
    return loadSessionsByProcessThunk(filters);
  }
  return loadSessionsThunk(filters);
}

