export function loadObjectsThunk(filters) {
  return async dispatch => {
    dispatch(creators.loading(LOAD_OBJECT_DETAILS));
    const objectApis = [
      { name: 'triggers', path: '/object_trigger' },
      { name: 'tables', path: '/object_table' },
      { name: 'indexes', path: '/object_index' },
      { name: 'procedures', path: '/object_procedure' },
      { name: 'packages', path: '/object_package' },
      { name: 'packageBodies', path: '/object_package_body' }
    ];
    try {
      let allData = await Promise.allSettled(
        objectApis.map(api =>
          RestClient.get(`/system/explorer${api.path}`, {
            dbms: filters.fullPath,
            page_size: 2147483645
          })
        )
      );
      allData = objectApis.map((api, index) => {
        return {
          res: allData[index]?.value?.data,
          name: api.name
        };
      });
      dispatch(creators.success(LOAD_OBJECT_DETAILS, { data: allData, key: filters.fullPath }));
    } catch (error) {
      dispatch(creators.fail(LOAD_OBJECT_DETAILS, error));
    }
  };
}

export function loadObjectsByProcessThunk(filters) {
  return async dispatch => {
    dispatch(creators.loading(LOAD_OBJECT_DETAILS));

    try {
      const { fullPath, protocol } = filters;
      const payload = {
        parameters: {
          dbconn: fullPath,
          oformat: 'JSON',
          compact: true
        },
        process: SystemExplorerFeatureUtil.getObjectsEndpoint(protocol),
        timestamp: null
      };

      let objectsJSON = [];
      const creationResult = await postInvocation(payload);
      if (creationResult.invocations.length) {
        const invocationUuid = creationResult.invocations[0].uuid;
        const output = await invocationOutputPoller(invocationUuid);
        objectsJSON = parseInvocationResult(output);
      }

      const allData = objectsJSON.map((item) => {
        return {
          res: item,
          name: item.name
        };
      });
      dispatch(creators.success(LOAD_OBJECT_DETAILS, { data: allData, key: filters.fullPath }));
    } catch (error) {
      dispatch(creators.fail(LOAD_OBJECT_DETAILS, error));
    }
  };
}

export function objectsLoaderResolver(filters) {
  const { protocol } = filters;
  if (SystemExplorerFeatureUtil.shouldLoadObjectsByProcess(protocol)) {
    return loadObjectsByProcessThunk(filters);
  }
  return loadObjectsThunk(filters);
}

