export function provideServersUsageThunk() {
  return async function serversUsage(dispatch, getState) {
    let servers = [];

    if (getState().explorer.structured.dbms.find(d => d.protocol === 'ORACLE')) {
      servers = getState()
        .explorer.structured.dbms.find(d => d.protocol === 'ORACLE')
        .servers.slice();
    }

    const usageResult = { servers: [] };

    const allResults = await Promise.allSettled(
      servers.map(server => {
        return loadUsagePerDb(server);
      })
    );
    allResults
      .filter(result => result.status === 'fulfilled')
      .map(result => {
        usageResult.servers.push({
          dbms: result.value.dbms,
          address: result.value.server,
          usage: result.value.usage || 0
        });

        return null;
      });

    usageResult.highLoadCount = usageResult.servers.filter(server => server.usage >= 75).length;
    usageResult.servers.sort((a, b) => parseFloat(b.usage) - parseFloat(a.usage));

    dispatch(creators.success(PROVIDE_SERVERS_USAGE, usageResult));

    return usageResult;
  };
}

export function provideDBLastBackupThunks() {
  return async function getDBLastBackup(dispatch, getState) {
    const { structured } = getState().explorer;
    const Oracle = structured.dbms.find(r => r.protocol === 'ORACLE');
    const Mysql = structured.dbms.find(r => r.protocol === 'MYSQL');
    const Pgsql = structured.dbms.find(r => r.protocol === 'PGSQL');
    const Mssql = structured.dbms.find(r => r.protocol === 'MSSQL');

    let servers = [];
    let databases = [];

    // for Oracle Dbms do a call for all its databases
    if (Oracle) {
      databases = Oracle.databases;
      servers = Oracle.servers;
    }

    // for Myssql Dbms do a call only for mysql databases
    if (Mysql) {
      databases = [...databases, ...Mysql.databases.filter(db => db.special.database === 'mysql')];
      servers = [...servers, ...Mysql.servers];
    }

    // for Pgsql dbms do a call only for posgres databases
    if (Pgsql) {
      databases = [
        ...databases,
        ...Pgsql.databases.filter(db => db.special.database === 'postgres')
      ];
      servers = [...servers, ...Pgsql.servers];
    }

    // for Mssql dbms do a call only for master databases
    if (Mssql) {
      databases = [...databases, ...Mssql.databases.filter(db => db.special.database === 'master')];
      servers = [...servers, ...Mssql.servers];
    }

    const dbLastBackup = [];

    const allResults = await Promise.allSettled(databases.map((db) => loadDBLastBackup(db, servers)));

    allResults
      .filter(result => result.status === 'fulfilled')
      .map(result => {
        dbLastBackup.push({
          name: result.value.name,
          backup: result.value.backup
        });

        return null;
      });

    dispatch(creators.success(PROVIDE_DB_LAST_BACKUP, dbLastBackup));

    return dbLastBackup;
  };
}

export function provideDBStatusThunks() {
  return async function getDBStatus(dispatch, getState) {
    const { structured } = getState().explorer;
    const Oracle = structured.dbms.find(r => r.protocol === 'ORACLE');
    const Mysql = structured.dbms.find(r => r.protocol === 'MYSQL');
    const Pgsql = structured.dbms.find(r => r.protocol === 'PGSQL');
    const Mssql = structured.dbms.find(r => r.protocol === 'MSSQL');

    let servers = [];
    let databases = [];

    // for Oracle Dbms do a call for all its databases
    if (Oracle) {
      databases = Oracle.databases;
      servers = Oracle.servers;
    }

    // for Myssql Dbms do a call only for mysql databases
    if (Mysql) {
      databases = [...databases, ...Mysql.databases];
      servers = [...servers, ...Mysql.servers];
    }

    // for Pgsql dbms do a call only for posgres databases
    if (Pgsql) {
      databases = [...databases, ...Pgsql.databases];
      servers = [...servers, ...Pgsql.servers];
    }

    // for Mssql dbms do a call only for master databases
    if (Mssql) {
      databases = [...databases, ...Mssql.databases];
      servers = [...servers, ...Mssql.servers];
    }

    const dbStatus = [];
    const allResults = await Promise.allSettled(
      databases.map((db) => loadDBStatus(db, servers))
    );

    allResults
      .filter(result => result.status === 'fulfilled')
      .map(result => {
        dbStatus.push({
          name: result.value.name,
          status: result.value.status,
          server: result.value.server,
          dbms: result.value.dbms,
          message: result.value.message,
          dbName: result.value.dbName
        });

        return null;
      });

    dispatch(creators.success(PROVIDE_DB_STATUS, dbStatus));

    return dbStatus;
  };
}

export function refreshServerDbsStatusThunk(server) {
  return async function refreshDbStatus(dispatch, getState) {
    const { databases } = server;

    async function retrieveDBStatus(database) {
      dispatch(
        creators.loading(UPDATE_DB_STATUS, [
          {
            name: database.name,
            server: database.address,
            dbms: database.protocol,
            dbName: database?.special?.database,
            status:
              getState().explorer.dbStatus.find(db => db.name === database.name)?.status ||
              'not-connected'
          }
        ])
      );

      const dbms = database.name;
      const shell = server.name;
      try {
        const response = await RestClient.get(CONSTANTS.BASE.ENDPOINTS.DB_STATUS, { dbms, shell });

        let status = response.data.status?.toString().toLowerCase() || 'error';
        // there is a case where API simply returns only 64, for not it will be treated as down
        if (parseInt(response.data, 10) === 64) {
          status = 'down';
        }

        return {
          name: database.name,
          server: database.address,
          dbms: database.protocol,
          dbName: database?.special?.database,
          message: response?.data?.message,
          status
        };
      } catch (error) {
        dispatch(
          creators.fail(UPDATE_DB_STATUS, [
            {
              name: database.name,
              server: database.address,
              dbms: database.protocol,
              dbName: database?.special?.database,
              status: 'error',
              error
            }
          ])
        );

        return {
          name: database.name,
          server: database.address,
          dbms: database.protocol,
          dbName: database?.special?.database,
          status: 'error',
          error
        };
      }
    }

    const dbStatus = [];
    const allResults = await Promise.allSettled(databases.map(retrieveDBStatus));

    allResults
      .filter(result => result.status === 'fulfilled')
      .map(result => {
        dbStatus.push({
          name: result.value.name,
          status: result.value.status,
          server: result.value.server,
          dbms: result.value.dbms,
          dbName: result.value.dbName,
          message: result.value.message
        });

        return null;
      });

    dispatch(creators.success(UPDATE_DB_STATUS, dbStatus));

    return dbStatus;
  };
}

export function refreshDBStatusThunk(db) {
  return async function getDBStatus(dispatch, getState) {
    const { structured } = getState().explorer;

    const selectedDBMS = structured.dbms.find(r => r.protocol === db.protocol);

    const dbms = db.name;
    const shell = selectedDBMS
      ? selectedDBMS.servers.find(sv => sv.address === db.address)?.name
      : undefined;

    dispatch(
      creators.loading(UPDATE_DB_STATUS, [
        {
          name: db.name,
          server: db.address,
          dbms: db.protocol,
          status: getState().explorer.dbStatus.find(d => d.name === db.name)?.status
        }
      ])
    );

    try {
      const response = await RestClient.get(CONSTANTS.BASE.ENDPOINTS.DB_STATUS, {
        dbms,
        shell
      });

      let status = response.data.status?.toString().toLowerCase() || 'error';
      // there is a case where API simply returns only 64, for not it will be treated as down
      if (parseInt(response.data, 10) === 64) {
        status = 'down';
      }

      const dbStatus = [
        {
          name: dbms,
          server: db.address,
          dbms: db.protocol,
          status
        }
      ];
      dispatch(creators.success(UPDATE_DB_STATUS, dbStatus));
      return dbStatus;
    } catch (error) {
      dispatch(
        creators.fail(UPDATE_DB_STATUS, [
          {
            name: db.name,
            server: db.address,
            dbms: db.protocol,
            status: 'error',
            error
          }
        ])
      );
    }
  };
}

