async function loadUsagePerDb(server) {
  const database = server.databases[0];
  const response = await RestClient.get(`/system/explorer/${database.name}/cpu_load_server`);

  const usage = parseFloat(response.data.HOST_CPU);

  return {
    dbms: database.protocol,
    server: database.address,
    usage
  };
}

async function loadDBLastBackup(database, servers) {
  const dbms = database.name;
  const shell = servers.find(
    s => s.databases && s.databases.find(d => d.name === database.name)
  )?.name;

  const response = await RestClient.get(CONSTANTS.BASE.ENDPOINTS.DB_LAST_BACKUP, {
    dbms,
    shell
  });

  return {
    name: database.name,
    backup: moment(response.data.backup).isValid()
      ? moment(response.data.backup).format('MMM DD YYYY')
      : null
  };
}

async function loadDBStatus(database, servers) {
  const dbms = database.name;
  const shell = servers.find(
    s => s.databases && s.databases.find(d => d.name === database.name)
  )?.name;

  try {
    const response = await RestClient.get(CONSTANTS.BASE.ENDPOINTS.DB_STATUS, {
      dbms,
      shell
    });

    let status = response.data.status?.toString().toLowerCase() || 'error';
    // there is a case where API simply returns only 64, for not it will be treated as down
    if (parseInt(response.data, 10) === 64) {
      status = 'down';
    }

    return {
      name: database.name,
      server: database.address,
      dbms: database.protocol,
      message: response?.data?.message,
      dbName: database?.special?.database,
      status
    };
  } catch (error) {
    return {
      name: database.name,
      server: database.address,
      dbms: database.protocol,
      message: database?.data?.message,
      dbName: database?.special?.database,
      status: 'error'
    };
  }
}
