import React from 'react';
import * as PropTypes from 'prop-types';
import './Header.local.scss';

export const Header = ({ title, children, stretchContent }) => (
  <header className={`header ${stretchContent ? 'stretched-content' : ''}`}>
    { title && <h1 className="header-title">{title}</h1>}
    {children}
  </header>
);

Header.propTypes = {
  title: PropTypes.string,
  children: PropTypes.node,
};

Header.defaultProps = {
  title: '',
};
