import React from 'react';
import SvgIcon from '@material-ui/core/SvgIcon';
import { copyTextToClipboard } from '../../../../license-collection/utils/copyToClipboard';
import Icon from '@mdi/react';
import { mdiContentCopy } from '@mdi/js';
import { formatTimePeriod } from '../../../../license-collection/utils/formatTimePeriod';
import CircularProgress from '@material-ui/core/CircularProgress';
import { Center, LightTooltip, Table } from '../../../../shared/components';
import PropTypes from 'prop-types';
import '../TopSQLStatements.local.scss';

const columns = [
  { label: 'Session ID' },
  { label: 'SQL ID' },
  { label: 'Child Number' },
  { label: 'SQL Text' },
  { label: 'Module' },
  { label: 'Executions' },
  { label: 'Elapsed Time', sortKey: 'ELAPSED_TIME' },
  { label: 'Elapsed Per Exec' },
  { label: 'CPU, %' },
  { label: 'I/O, %' }
];

const CellWithTooltip = ({ name }) => {
  if (name && name.length > 16) {
    return (
      <LightTooltip placement="bottom" title={<span>{name}</span>}>
        <span className="statement-cell ellipsis">{name}</span>
      </LightTooltip>
    );
  }
  return <span className="statement-cell">{name}</span>;
};

const rowRenderer = (row, index) => (
  <Table.Row hover key={`${row.sid}_${index}`} className="statement-row">
    <Table.Cell>
      <CellWithTooltip name={row.SESS_ID} />
    </Table.Cell>
    <Table.Cell>
      <CellWithTooltip name={row.SQL_ID} />
    </Table.Cell>
    <Table.Cell>
      <CellWithTooltip name={row.CHILD_NUMBER} />
    </Table.Cell>
    <Table.Cell>
      <LightTooltip placement="top" title="Copy to clipboard">
        <SvgIcon className="copy-icon" onClick={() => { copyTextToClipboard(row.SQL_TEXT); }}>
          <Icon path={mdiContentCopy} />
        </SvgIcon>
      </LightTooltip>
      <CellWithTooltip name={row.SQL_TEXT} />
    </Table.Cell>
    <Table.Cell>
      <CellWithTooltip name={row.MODULE} />
    </Table.Cell>
    <Table.Cell>
      <CellWithTooltip name={row.EXECUTIONS} />
    </Table.Cell>
    <Table.Cell>
      <CellWithTooltip name={formatTimePeriod(Math.round(parseInt(row.ELAPSED_TIME, 10) / 1000))} />
    </Table.Cell>
    <Table.Cell>
      <CellWithTooltip name={formatTimePeriod(Math.round(parseInt(row.ELAPSED_PER_EXEC, 10)))} />
    </Table.Cell>
    <Table.Cell>
      <CellWithTooltip name={row.PCT_CPU} />
    </Table.Cell>
    <Table.Cell>
      <CellWithTooltip name={row.PCT_IO} />
    </Table.Cell>
  </Table.Row>
);

const TableRenderer = ({ isLoading, sqlStatements, noDataMessage, tableProps }) => {
  if (isLoading) {
    return (
      <Center>
        <CircularProgress />
      </Center>
    );
  }
  if (sqlStatements.length === 0) {
    return <Center>{noDataMessage}</Center>;
  }
  return <Table {...tableProps}>{sqlStatements.map(rowRenderer)}</Table>;
};

export class OracleTopSQLTable extends React.PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      sortBehavior: { sortKey: 'ELAPSED_TIME', order: 'desc' }
    };
  }

  render() {
    const {
      topSqlStatements,
      isLoading
    } = this.props;

    const { sortBehavior } = this.state;
    const tableProps = { columns, sortBehavior, changeSort: this.changeSort };
    const sortedStatements = this.sortSqlStatements(topSqlStatements);

    return (
      <>
        <TableRenderer
          isLoading={isLoading}
          sqlStatements={sortedStatements}
          tableProps={tableProps}
          noDataMessage="No Statements Available"
        />
      </>
    );
  }

  changeSort = (sortKey, order) => () => {
    this.setState({ sortBehavior: { sortKey, order } });
  };

  sortSqlStatements = (topStatementsData) => {
    const { sortBehavior } = this.state;
    return (topStatementsData || []).slice().sort((o1, o2) => {
      const orderMultiplier = sortBehavior.order === 'asc' ? 1 : -1;
      return (o1.ELAPSED_TIME - o2.ELAPSED_TIME) * orderMultiplier;
    });
  }
}

OracleTopSQLTable.propTypes = {
  isLoading: PropTypes.bool,
  topSqlStatements: PropTypes.array
};
