import React from 'react';
import { LightTooltip, Table } from '../../../../shared/components';
import PropTypes from 'prop-types';
import SvgIcon from '@material-ui/core/SvgIcon';
import Icon from '@mdi/react';
import { mdiCheckboxMarkedCircle, mdiCloseCircle } from '@mdi/js';
import moment from 'moment';

import './FeaturesTable.scss';

const columns = [
  { label: 'Feature Name', sortKey: 'NAME' },
  { label: 'Version' },
  { label: 'Usages', sortKey: 'USAGES' },
  { label: 'First Usage' },
  { label: 'Last Usage' },
  { label: 'Status', sortKey: 'STATUS' },
];

const CellWithTooltip = ({ name }) => {
  if (name && name.length > 16) {
    return (
      <LightTooltip placement="bottom" title={<span>{name}</span>}>
        <span className="license-feature-cell ellipsis">{name}</span>
      </LightTooltip>
    );
  }
  return <span className="license-feature-cell">{name}</span>;
};

const rowRenderer = (row, index,) => (
  <Table.Row hover key={`${row.NAME}_${index}`} className="license-feature-row">
    <Table.Cell>
      <CellWithTooltip name={row.NAME} />
    </Table.Cell>
    <Table.Cell>
      <span className="license-feature-cell">{row.VERSION}</span>
    </Table.Cell>
    <Table.Cell>
      <span className="license-feature-cell">{row.DETECTED_USAGES}</span>
    </Table.Cell>
    <Table.Cell>
      <span className="license-feature-cell">
        {moment(row.FIRST_USAGE_DATE).isValid() ? moment(row.FIRST_USAGE_DATE).format('DD-MMM-YYYY mm:HH:ss') : ''}
      </span>
    </Table.Cell>
    <Table.Cell>
      <span className="license-feature-cell">
        {moment(row.LAST_USAGE_DATE).isValid() ? moment(row.LAST_USAGE_DATE).format('DD-MMM-YYYY mm:HH:ss') : ''}
      </span>
    </Table.Cell>
    <Table.Cell>
      <span className="license-feature-cell">
        <div className="status-container">
          {row.CURRENTLY_USED === 'TRUE' ? (
            <div className="status available">
              <SvgIcon>
                <Icon path={mdiCheckboxMarkedCircle} />
              </SvgIcon>
              Available
            </div>
          ) : (
            <div className="status unavailable">
              <SvgIcon>
                <Icon path={mdiCloseCircle} />
              </SvgIcon>
              Unavailable
            </div>
          )}
        </div>
      </span>
    </Table.Cell>
  </Table.Row>
);

const TableRenderer = ({ licenseFeatures, tableProps }) => {
  return <Table {...tableProps}>{licenseFeatures.map((row, index) => rowRenderer(row, index))}</Table>;
};

export class FeaturesTable extends React.PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      sortBehavior: { sortKey: 'NAME', order: 'asc' },
    };
  }

  render() {
    const { licenseFeatures } = this.props;
    const { sortBehavior } = this.state;
    const tableProps = { columns, sortBehavior, changeSort: this.changeSort };
    const sorted = this.sortLicenseFeatures(licenseFeatures);

    return (
      <TableRenderer
        licenseFeatures={sorted}
        tableProps={tableProps}
      />
    );
  }

  changeSort = (sortKey, order) => () => {
    this.setState({ sortBehavior: { sortKey, order } });
  };

  sortLicenseFeatures = (licenseFeatures) => {
    const { sortBehavior } = this.state;
    const key = sortBehavior.sortKey;
    const orderMultiplier = sortBehavior.order === 'asc' ? 1 : -1;
    switch (key) {
      case 'NAME': {
        return this.sortByName(licenseFeatures, orderMultiplier);
      }
      case 'USAGES': {
        return this.sortByUsages(licenseFeatures, orderMultiplier);
      }
      case 'STATUS': {
        return this.sortByStatus(licenseFeatures, orderMultiplier);
      }
      default:
        return licenseFeatures;
    }
  };

  sortByName = (licenseFeatures, order) => {
    return (licenseFeatures || []).slice().sort((o1, o2) => {
      return (o1.NAME.localeCompare(o2.NAME)) * order;
    });
  };

  sortByUsages = (licenseFeatures, order) => {
    return (licenseFeatures || []).slice().sort((o1, o2) => {
      return (o1.DETECTED_USAGES - o2.DETECTED_USAGES) * order;
    });
  };

  sortByStatus = (licenseFeatures, order) => {
    return (licenseFeatures || []).slice().sort((o1, o2) => {
      const firstUsage = o1.CURRENTLY_USED === 'TRUE' ? 1 : -1;
      const secondUsage = o2.CURRENTLY_USED === 'TRUE' ? 1 : -1;
      return (firstUsage - secondUsage) * order;
    });
  };
}

FeaturesTable.propTypes = {
  licenseFeatures: PropTypes.array,
};
