import { DbmsType } from '../../SystemExplorerFeatureUtil';

const ALLOWED_DBMS = [DbmsType.ORACLE, DbmsType.POSTGRES, DbmsType.MYSQL, DbmsType.MSSQL];
const ALLOWED_FOR_CHARTS = [DbmsType.ORACLE];

const PROCESS_ENDPOINT_MAP = {
    [DbmsType.ORACLE]: 'vendita.oracle.rdbms.util.monitor.session.performance.topsql',
    [DbmsType.POSTGRES]: 'vendita.pgsql.rdbms.session.topsql_postgres',
    [DbmsType.MYSQL]: 'vendita.mysql.rdbms.session.topsql_mysql',
    [DbmsType.MSSQL]: 'vendita.microsoft.sqlserver.monitor.tuning.top50longrunningsql',
};

const DBMS_PROPERTY_NAME_MAP = {
    [DbmsType.ORACLE]: 'dbms',
    [DbmsType.POSTGRES]: 'dbms',
    [DbmsType.MYSQL]: 'dbms',
    [DbmsType.MSSQL]: 'dbconn',
};

export class TopSqlStatementsUtil {
  static isAllowedProtocol(dbms) {
    return ALLOWED_DBMS.includes(dbms);
  }

  static isChartSupported(dbms) {
    return ALLOWED_FOR_CHARTS.includes(dbms);
  }

  static getEndpoint(dbms) {
    return PROCESS_ENDPOINT_MAP[dbms] || '';
  }

  static getDbmsPropertyName(dbms) {
    return DBMS_PROPERTY_NAME_MAP[dbms] || '';
  }
}
