import React from 'react';
import { func, string } from 'prop-types';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import './NavigationTab.local.scss';
import { TopSqlStatementsUtil } from '../../containers/TopSQLStatements/TopSqlStatementsUtil';
import { SystemExplorerFeatureUtil } from '../../SystemExplorerFeatureUtil';

export class NavigationTab extends React.PureComponent {
  render() {
    const { active, onClick, protocol } = this.props;
    const isOracle = protocol === 'ORACLE';

    const data = [
      { label: 'Workload', value: 'workload' },
      TopSqlStatementsUtil.isAllowedProtocol(protocol) ? { label: 'Top SQL statements', value: 'topsql' } : false,
      SystemExplorerFeatureUtil.isSessionsAllowed(protocol) ? { label: 'Sessions', value: 'sessions' } : false,
      SystemExplorerFeatureUtil.isObjectsAllowed(protocol) ? { label: 'Objects', value: 'objects' } : false,
      isOracle ? { label: 'Storage', value: 'storage' } : false
    ].filter(Boolean);

    if (protocol === 'ORACLE') {
      data.push({ label: 'License reporting', value: 'license' });
    }

    return (
      <div className="navigation-tab">
        <Tabs
          value={data.map(e => e.value).indexOf(active)}
          indicatorColor="primary"
          textColor="primary"
        >
          {data.map(tab => (
            <Tab
              key={tab.label}
              label={tab.label}
              onClick={() => onClick(tab.value)}
              data-qa={`navigate-to-${tab.value}`}
            />
          ))}
        </Tabs>
      </div>
    );
  }
}

NavigationTab.propTypes = {
  active: string,
  onClick: func
};

export default NavigationTab;
