import React from 'react';
import SvgIcon from '@material-ui/core/SvgIcon';
import { copyTextToClipboard } from '../../../../license-collection/utils/copyToClipboard';
import Icon from '@mdi/react';
import { mdiContentCopy } from '@mdi/js';
import { formatTimePeriod } from '../../../../license-collection/utils/formatTimePeriod';
import CircularProgress from '@material-ui/core/CircularProgress';
import { Center, LightTooltip, Table } from '../../../../shared/components';
import PropTypes from 'prop-types';
import '../TopSQLStatements.local.scss';

const columns = [
  { label: 'Database Name' },
  { label: 'SQL Text' },
  { label: 'Executions' },
  { label: 'Total CPU Time' },
  { label: 'Avg CPU Time' },
  { label: 'Max Elapsed Time', sortKey: 'MaxElapsedTime' },
  { label: 'Avg Elapsed Time' },
  { label: 'Logical Reads' },
  { label: 'Physical Reads' },
  { label: 'Logical Writes' },
];

const CellWithTooltip = ({ name }) => {
  if (name && name.length > 16) {
    return (
      <LightTooltip placement="bottom" title={<span>{name}</span>}>
        <span className="statement-cell ellipsis">{name}</span>
      </LightTooltip>
    );
  }
  return <span className="statement-cell">{name}</span>;
};

const rowRenderer = (row, index) => (
  <Table.Row hover key={`${row.Database_name}_${index}`} className="statement-row">
    <Table.Cell>
      <CellWithTooltip name={row.Database_name} />
    </Table.Cell>
    <Table.Cell>
      <LightTooltip placement="top" title="Copy to clipboard">
        <SvgIcon className="copy-icon" onClick={() => { copyTextToClipboard(row.QueryName); }}>
          <Icon path={mdiContentCopy} />
        </SvgIcon>
      </LightTooltip>
      <CellWithTooltip name={row.QueryName} />
    </Table.Cell>
    <Table.Cell>
      <CellWithTooltip name={row.ExecutionCount} />
    </Table.Cell>
    <Table.Cell>
      <CellWithTooltip name={formatTimePeriod(Math.round(parseInt(row['Total_CPU_Time(second)'], 10) * 1000))} />
    </Table.Cell>
    <Table.Cell>
      <CellWithTooltip name={formatTimePeriod(parseInt(row['Average_CPU_Time(milisecond)'], 10))} />
    </Table.Cell>
    <Table.Cell>
      <CellWithTooltip name={formatTimePeriod(parseInt(row.MaxElapsedTime, 10))} />
    </Table.Cell>
    <Table.Cell>
      <CellWithTooltip name={formatTimePeriod(parseInt(row.AvgElapsedTime, 10))} />
    </Table.Cell>
    <Table.Cell>
      <CellWithTooltip name={row.Logical_Reads} />
    </Table.Cell>
    <Table.Cell>
      <CellWithTooltip name={row.physical_reads} />
    </Table.Cell>
    <Table.Cell>
      <CellWithTooltip name={row.logical_writes} />
    </Table.Cell>
  </Table.Row>
);

const TableRenderer = ({ isLoading, sqlStatements, noDataMessage, tableProps }) => {
  if (isLoading) {
    return (
      <Center>
        <CircularProgress />
      </Center>
    );
  }
  if (sqlStatements.length === 0) {
    return <Center>{noDataMessage}</Center>;
  }
  return <Table {...tableProps}>{sqlStatements.map(rowRenderer)}</Table>;
};

export class MssqlTopSQLTable extends React.PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      sortBehavior: { sortKey: 'MaxElapsedTime', order: 'desc' }
    };
  }

  render() {
    const {
      topSqlStatements,
      isLoading
    } = this.props;

    const { sortBehavior } = this.state;
    const tableProps = { columns, sortBehavior, changeSort: this.changeSort };
    const sortedStatements = this.sortSqlStatements(topSqlStatements);

    return (
      <>
        <TableRenderer
          isLoading={isLoading}
          sqlStatements={sortedStatements}
          tableProps={tableProps}
          noDataMessage="No Statements Available"
        />
      </>
    );
  }

  changeSort = (sortKey, order) => () => {
    this.setState({ sortBehavior: { sortKey, order } });
  };

  sortSqlStatements = (topStatementsData) => {
    const { sortBehavior } = this.state;
    return (topStatementsData || []).slice().sort((o1, o2) => {
      const orderMultiplier = sortBehavior.order === 'asc' ? 1 : -1;
      return (o1.MaxElapsedTime - o2.MaxElapsedTime) * orderMultiplier;
    });
  }
}

MssqlTopSQLTable.propTypes = {
  isLoading: PropTypes.bool,
  topSqlStatements: PropTypes.array
};
