import React from 'react';
import PropTypes from 'prop-types';
import { StorageSidebar, ObjectsSidebar } from './Sidebar';
import { StorageViewer, ObjectsViewer } from './Viewer';

import './root.local.scss';
import { SystemExplorerFeatureUtil } from '../../SystemExplorerFeatureUtil';

export const DbObjects = ({
  type,
  data,
  list,
  dbName,
  protocol,
  selectedItem,
  setItem,
  loadingFlags,
  invalidObjects
}) => {
  const { loadingPoints, loadingDetails, loading } = loadingFlags;
  const sidebarDisabled = SystemExplorerFeatureUtil.isObjectsSidebarDisabled(protocol);
  if (type === 'storage') {
    return (
      <div className="db-objects-tabs">
        <StorageSidebar
          dbName={dbName}
          isLoading={loadingPoints}
          list={list}
          selectedItem={selectedItem}
          setItem={setItem}
        />
        <StorageViewer data={data} isLoading={loadingDetails} />
      </div>
    );
  }
  return (
    <div className="db-objects-tabs">
      {!sidebarDisabled && (
        <ObjectsSidebar
          dbName={dbName}
          invalidObjects={invalidObjects}
          isLoading={loading}
          list={list}
          selectedItem={selectedItem}
          setItem={setItem}
        />
      )}
      <ObjectsViewer data={data} isLoading={loading} sidebarDisabled={sidebarDisabled} protocol={protocol} />
    </div>
  );
};

DbObjects.propTypes = {
  type: PropTypes.string,
  data: PropTypes.object,
  list: PropTypes.array,
  selectedItem: PropTypes.object,
  setItem: PropTypes.func,
  dbName: PropTypes.string,
  protocol: PropTypes.string
};
