import React from 'react';
import { connect } from 'react-redux';
import * as PropTypes from 'prop-types';
import Tooltip from '@material-ui/core/Tooltip';
import { withStyles } from '@material-ui/core/styles';
import { actions } from 'session/store/actions';
import { beHealthSelector, beLicenceSelector, beVersionSelector, currentUserSelector } from 'session/store/state';
import { Compile, Navigation, UserMenu } from './components';
import pkg from '../../../../../package.json';
import MasLogo from '../../../../assets/images/mas_logo_white.svg';
import VenditaLogo from '../../../../assets/images/vendita_logo_white.svg';
import './sidebar.local.scss';

const RUNNING = {
  height: '10px',
  width: '10px',
  backgroundColor: 'rgb(79, 219, 70)',
  borderRadius: '50%',
  position: 'relative',
  left: '6px',
  top: '20px'
};

const STATUS_SPAN = {
  color: '#fff',
  fontSize: '11px',
  marginLeft: '14px',
  textShadow: '1px 1px 1px #3765af3b',
  position: 'relative',
  top: '-2px'
};

const STOPPED = {
  height: '10px',
  width: '10px',
  backgroundColor: 'red',
  borderRadius: '50%',
  position: 'relative',
  left: '6px',
  top: '20px'
};

export class SidebarContainer extends React.PureComponent {
  componentDidMount() {
    this.props.getBeVersion();
    this.props.getBeLicence();
    this.props.getBeHealth();
  }

  render() {
    const postgresStatus = this.props.beHealth ? this.props.beHealth.masSystemsMonitor.postgresStatus : null;
    const masDaemonStatus = this.props.beHealth ? this.props.beHealth.masSystemsMonitor.masDaemonStatus : null;
    const uptime1Minute = this.props.beHealth
      ? `${this.props.beHealth.masSystemsMonitor.uptime1Minute},${this.props.beHealth.masSystemsMonitor.uptime5Minutes},${this.props.beHealth.masSystemsMonitor.uptime15Minutes}`
      : null;

    const daysUntilExpire = this.props.beLicence ? this.props.beLicence.licensed_feature[1].daysUntilExpire : null;
    const expirationDate = this.props.beLicence ? this.props.beLicence.licensed_feature[1].expirationDate : null;
    const licenseStatus = this.props.beLicence ? this.props.beLicence.licensed_feature[1].licenseStatus : null;

    return (
      <div className={this.props.className}>
        <div className="logo-container">
          <img className="vendita-logo img-responsive" src={VenditaLogo} alt="Vendita logo" />
          <LightTooltip
            placement="right"
            title={
              <>
                <div>
                  <span style={{ paddingRight: '2px' }}>UI </span>version:&nbsp;
                  {pkg.version}
                </div>
                <div>
                  <span>BE </span>version:&nbsp;
                  {this.props.beVersion}
                </div>

                <div>
                <span>BE </span>License: {(licenseStatus ? 'MAS Lite' : 'MAS Full')} <br />
                  Expiration Date: {expirationDate} <br />
                  Days Until Expire: {daysUntilExpire} <br />
                </div>
              </>
            }
          >
            <img className="mas-logo img-responsive" src={MasLogo} alt="MAS logo" />
          </LightTooltip>
        </div>
        <div className="nav-area">
          <div style={{ position: 'relative', bottom: '17px', right: '2px' }}>
          {masDaemonStatus && (
            <div style={{ top: '14px', position: 'relative' }}>
              <div style={RUNNING}>
                <span style={STATUS_SPAN}><a target="_blank" href="/masmon.html">MAS</a></span>
              </div>
            </div>
          )}
          {!masDaemonStatus && (
            <div style={{ top: '14px', position: 'relative' }}>
              <div style={STOPPED}>
                <span style={STATUS_SPAN}><a target="_blank" href="/masmon.html">MAS</a></span>
              </div>
            </div>
          )}
          {postgresStatus && (
            <div style={{ top: '18px', position: 'relative' }}>
              <div style={RUNNING}>
                <span style={STATUS_SPAN}><a target="_blank" href="/masmon.html">Postgres</a></span>
              </div>
            </div>
          )}
          {!postgresStatus && (
            <div style={{ top: '18px', position: 'relative' }}>
              <div style={STOPPED}>
                <span style={STATUS_SPAN}><a target="_blank" href="/masmon.html">Postgres</a></span>
              </div>
            </div>
          )}
          {uptime1Minute && (
            <div style={{ top: '23px', position: 'relative' }}>
              <div style={RUNNING}>
                <span style={STATUS_SPAN}><a target="_blank" href="/masmon.html">Load {uptime1Minute && uptime1Minute}</a></span>
              </div>
            </div>
          )}
          {!uptime1Minute && (
            <div style={{ top: '20px', position: 'relative' }}>
              <div style={STOPPED}>
                <span style={STATUS_SPAN}><a target="_blank" href="/masmon.html">Load {uptime1Minute && uptime1Minute}</a></span>
              </div>
            </div>
          )}
          </div>
          <Navigation />
        </div>
        <div className="bottom">
          <Compile />
          <UserMenu user={this.props.currentUser.get('name')} />
        </div>
      </div>
    );
  }
}

SidebarContainer.propTypes = {
  className: PropTypes.string,
  currentUser: PropTypes.object.isRequired
};

export const mapStateToProps = state => ({
  currentUser: currentUserSelector(state),
  beVersion: beVersionSelector(state),
  beLicence: beLicenceSelector(state),
  beHealth: beHealthSelector(state)
});

export const mapDispatchToProps = dispatch => ({
  getBeVersion: data => dispatch(actions.getBeVersion.dispatchLoading(data)),
  getBeLicence: data => dispatch(actions.getBeLicence.dispatchLoading(data)),
  getBeHealth: data => dispatch(actions.getBeHealth.dispatchLoading(data))
});

SidebarContainer.defaultProps = {
  className: 'sidebar'
};

export default connect(mapStateToProps, mapDispatchToProps)(SidebarContainer);

const LightTooltip = withStyles(() => ({
  tooltip: {
    color: '#fff',
    backgroundColor: '#787E89',
    letterSpacing: '0.4px',
    fontSize: '12px',
    lineHeight: '16px',
    fontFamily: 'Open Sans'
  }
}))(Tooltip);
