import React from 'react';
import CircularProgress from '@material-ui/core/CircularProgress';
import { Center, LightTooltip, Table } from '../../../../shared/components';
import PropTypes from 'prop-types';
import '../Sessions.local.scss';

const columns = [
  { label: 'Serial #' },
  { label: 'Username' },
  { label: 'Status' },
  { label: 'Machine' },
  { label: 'SID' },
  { label: 'Logon time', sortKey: 'LOGON_TIME' },
  { label: 'Program' },
  { label: 'Type' }
  // { label: '' } -> disabled view details button for now...
];

const CellWithTooltip = ({ name }) => {
  if (name && name.length > 16) {
    return (
      <LightTooltip placement="bottom" title={<span>{name}</span>}>
        <span className="session-cell ellipsis">{name}</span>
      </LightTooltip>
    );
  }
  return <span className="session-cell">{name}</span>;
};

const rowRenderer = (row, index) => (
  <Table.Row hover key={`${row.sid}_${index}`} className="session-row">
    <Table.Cell>
      <CellWithTooltip name={row['SERIAL#']} />
    </Table.Cell>
    <Table.Cell>
      <CellWithTooltip name={row.USERNAME} />
    </Table.Cell>
    <Table.Cell>
      <CellWithTooltip name={row.STATUS} />
    </Table.Cell>
    <Table.Cell>
      <CellWithTooltip name={row.MACHINE} />
    </Table.Cell>
    <Table.Cell>
      <CellWithTooltip name={row.SID} />
    </Table.Cell>
    <Table.Cell>
      <CellWithTooltip name={row.LOGON_TIME} />
    </Table.Cell>
    <Table.Cell>
      <CellWithTooltip name={row.PROGRAM} />
    </Table.Cell>
    <Table.Cell>
      <CellWithTooltip name={row.TYPE} />
    </Table.Cell>
    {/* <Table.Cell padding="none" className="view-details">
      <LightTooltip placement="bottom" title={<span>View Details</span>}>
        <SvgIcon>
          <Icon path={mdiViewCompact} />
        </SvgIcon>
      </LightTooltip>
    </Table.Cell> */}
  </Table.Row>
);

const TableRenderer = ({ isLoading, sessions, noDataMessage, tableProps }) => {
  if (isLoading) {
    return (
      <Center>
        <CircularProgress />
      </Center>
    );
  }
  if (sessions.length === 0) {
    return <Center>{noDataMessage}</Center>;
  }
  return <Table {...tableProps}>{sessions.map(rowRenderer)}</Table>;
};

export class CommonSessionsTable extends React.PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      sortBehavior: { sortKey: 'LOGON_TIME', order: 'asc' }
    };
  }

  render() {
    const {
      sessions,
      isLoading
    } = this.props;

    const { sortBehavior } = this.state;
    const tableProps = { columns, sortBehavior, changeSort: this.changeSort };
    const sortedSessions = this.sortSessions(sessions);

    return (
      <>
        <TableRenderer
          isLoading={isLoading}
          sessions={sortedSessions}
          tableProps={tableProps}
          noDataMessage="No Sessions Available"
        />
      </>
    );
  }

  changeSort = (sortKey, order) => () => {
    this.setState({ sortBehavior: { sortKey, order } });
  };

  sortSessions = (sessionsData) => {
    const { sortBehavior } = this.state;
    return (sessionsData || []).slice().sort((o1, o2) => {
      const orderMultiplier = sortBehavior.order === 'asc' ? 1 : -1;
      return (o1.LOGON_TIME - o2.LOGON_TIME) * orderMultiplier;
    });
  }
}

CommonSessionsTable.propTypes = {
  isLoading: PropTypes.bool,
  sessions: PropTypes.array
};
