import React from 'react';
import { connect } from 'react-redux';
import { bool, func, object } from 'prop-types';
import classnames from 'classnames';
import { Loader } from 'shared/components/loaders';
import { Center } from 'shared/components';
import { getLicenseReportThunk } from '../../../store/thunks';
import { FeaturesUsage } from '../../../components/LicenseReporting/FeaturesUsage/FeaturesUsage';
import { UnusedFeatures } from '../../../components/LicenseReporting/UnusedFeatures/UnusedFeatures';
import { getFullPath } from '../../../store/selectors';
import { FeaturesTable } from '../../../components/LicenseReporting/FeaturesTable/FeaturesTable';

import './Report.local.scss';

export class Report extends React.PureComponent {
  constructor(props) {
    super(props);

    this.state = {
      scrolled: false
    };
  }

  async componentDidMount() {
    await this.props.getLicenseReport(this.props.fullPath);
  }

  render() {
    const { scrolled } = this.state;
    const { isLoading, reportData, reportTwo } = this.props;
    return isLoading ? (
      <Center>
        <Loader>Loading report details</Loader>
      </Center>
    ) : (
      <div
        className={classnames('searchable-list-slot', {
          scrolled
        })}
      >
        <div className="container" onWheel={event => this.onWheel(event)}>
          {reportData && Object.keys(reportData.FEATURES).length > 0 && (
            <FeaturesTable licenseFeatures={reportData.FEATURES} />
          )}
          <FeaturesUsage reportOne={reportData} reportTwo={reportTwo} />
          <UnusedFeatures reportOne={reportData} reportTwo={reportTwo} />
        </div>
      </div>
    );
  }

  onWheel = event => {
    if (event.currentTarget.scrollTop) {
      if (!this.state.scrolled) {
        this.setState({ scrolled: true });
      }
    } else if (this.state.scrolled) {
      this.setState({ scrolled: false });
    }

    scroller(event);
  };
}

Report.propTypes = {
  getLicenseReport: func,
  isLoading: bool,
  reportData: object,
};

export const ConnectedReport = connect((state) => {
  const reportData = state.explorer.reports.data;
  return {
    isLoading: state.explorer.flags.reports.loading,
    fullPath: getFullPath(state),
    reportData
  };
}, dispatch => ({
  getLicenseReport: fullPath => dispatch(getLicenseReportThunk(fullPath))
}))(Report);

function scroller(event) {
  const target = event.currentTarget;

  target.scrollBy({
    top: event.deltaY,
    left: event.deltaX
  });
}
